<template>
    <v-card
        class="m-panel__options--3d"
        >
        <v-card-actions>
            <div
                v-for="(item, name) in options"
                :key="item.name"
                class="option--wrap">
                <v-tooltip
                    v-if="!item.hidden"
                    :disabled="!item.title"
                    top>
                    <template v-slot:activator="{ on, attrs }">
                        <v-btn
                            v-if="item.type == 'btn' || !item.type"
                            class="m-btn m-btn-icon--cube mx-1"
                            :class="{'m-btn--divider': item.divider, 'mr-2': item.divider}"
                            text
                            v-bind="attrs"
                            v-on="on"
                            :disabled="item.disabled"
                            @click="setOption(item, name)">
                            <v-icon
                                v-if="item.icon"
                                size="22"
                                :color="item.active && !item.turnoff ? '#4e93e9' : '#525252'">{{item.icon}}</v-icon>
                        </v-btn>
                        <span
                            v-else
                            class="m-text--size12"
                            >{{optionValue(item.value)}}</span>
                    </template>
                    <span>{{item.title}}</span>
                </v-tooltip>


                <v-card
                    v-if="item.children && item.active"
                    class="option--sub">
                    <div
                        class="option--sub-item"
                        v-for="(sub, n) in item.children"
                        :key="n"
                        @click="setSubOption(item, name, sub, n)"
                        >
                        <v-icon
                            size="24"
                            :color="sub.active ? '#4e93e9' : '#525252'">{{sub.icon}}</v-icon>
                        <span :class="{active: sub.active}">{{sub.title}}</span>
                    </div>
                </v-card>
            </div>
        </v-card-actions>
    </v-card>
</template>

<script>
export default {
    name: 'PanelOptions',
    props: ['options', 'params'],
    computed: {
        getOptions(){
            return this.options.filter(d => !d.hidden)
        }
    },
    methods: {
        setSubOption(i, name, sub, sub_name){
            this.$emit('setSubOption', {
                name: name,
                active: i.active,
                sub_name: sub_name,
                sub_active: sub.active
            })
        },
        setOption(i, n){
            this.$emit('setOption', {
                name: n,
                active: i.active,
                turnoff: i.turnoff || false
            })
        },
        optionValue(v){
            return this.params[v] || ''
        }
    }
}
</script>
