<template>
    <div class="m-comment-box">
        <div class="m-comment-list">
            <div
                class="m-comment-list--items"
                ref="list-items">
                <v-row
                    dense
                    v-for="(item, index) in commentList"
                    :key="'comment' + item.id">
                    <v-col cols="12">
                        <comment3d
                            :comment="item"
                            @getPosCamera="getPosCamera"
                            :posActive.sync="posActive"/>
                    </v-col>
                </v-row>
            </div>
        </div>
        <div class="m-comment-header">
            <v-row dense>
                <v-col cols="12">
                    <v-card
                        :outlined="true"
                        color="#fff"
                        class="m-comment-input"
                        :class="{active: focus}"
                        @mouseup="focusInput"
                        v-click-outside="focusInputRemove"
                        >
                        <v-container py-0>
                            <v-row>
                                <v-col cols="2" class="pb-0">
                                    <v-avatar
                                        size="30">
                                        <img
                                            src="https://cdn.vuetifyjs.com/images/john.jpg"
                                        >
                                    </v-avatar>
                                </v-col>
                                <v-col cols="10" class="py-0">
                                    <v-textarea
                                        id="comment"
                                        placeholder=""
                                        class="m-comment-no-border"
                                        v-model="comment"
                                        auto-grow
                                        outlined
                                        hide-details
                                        multi-line
                                        rows="2">
                                    </v-textarea>
                                </v-col>
                            </v-row>
                            <v-row v-show="focus">
                                <v-col cols="2" class="pt-0">
                                </v-col>
                                <v-col cols="10" class="pt-0">
                                    <v-card-actions class="pa-0">
                                        <v-btn
                                            :disabled="!comment"
                                            class="mt-2 m-btn"
                                            outlined
                                            dense
                                            color="primary"
                                            @click="saveComment"
                                        >
                                        
                                        </v-btn>
                                        <v-btn
                                            :disabled="!comment"
                                            class="mt-2 m-btn m-btn-normal"
                                            outlined
                                            dense
                                            color="normal"
                                            @click="clearComment"
                                        >
                                        
                                        </v-btn>
                                    </v-card-actions>
                                </v-col>
                            </v-row>
                        </v-container>
                    </v-card>
                </v-col>
            </v-row>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex"
import Comment3d from './comments/Comment3d'
import ClickOutside from '@/plugins/click_outside'

export default {
    name: 'Comments',
    components: {
        Comment3d
    },
    directives: {
        ClickOutside
    },
    props: ['getPosCamera', 'setPosCamera', 'documentId'],
    data(){
        return {
            comment: '',
            focus: false,
            posActive: 0
        }
    },
    computed: mapState({
        commentList(state){
            return state.workflow.commentList
        },
		markerStack(state){
            let list = state.workflow.markerStack
			return list
		},
		markerList(state){
			return state.workflow.markerList
		},
        markerEdit(state){
            return state.workflow.markerEdit
        }
    }),
    watch: {
        focus(v){
            this.focusComment(v)
        },
        posActive(v){
            this.scrollToComment(v)
        }
    },
    methods: {
        scrollToComment(v){
            this.$nextTick(() => {
                this.$refs['list-items'].scrollTo({top: v - 4, behavior: "smooth"})
            })
        },
        focusComment(v){
            this.$nextTick(() => {
                if(v){
                    let el = document.getElementById('comment')
                    el.focus()
                }
            })
        },
        saveComment: function(){
            let comment = {
                type: 'comment',
                avatar: 'https://cdn.vuetifyjs.com/images/john.jpg',
                title: '  1',
                user: ' ',
                text: this.comment,
                reply: new Array(),
                position: this.setPosCamera(),
                doc: '3d'
            }
            this.$store.dispatch('workflow/addComment', comment)
            this.comment = ''
            this.focus = false
        },
        clearComment(){
            this.comment = ''
            this.focus = false
        },
        focusInput(){
            this.focus = true
        },
        focusInputRemove(){
            if(this.focus && !this.comment){
                this.focus = false
            }
        }
    },
    created(){
        //this.$store.dispatch('workflow/setComments', {id: this.documentId, type: '3d'})
        let commentState = this.$store.state.workflow.commentState
        if(commentState.status){
            this.comment = commentState.commentInput
            this.$store.commit('workflow/setActiveComment', commentState.commentActive)
        }
    },
    beforeDestroy(){
        this.$store.dispatch('workflow/saveCommentState', {input: this.comment})
        this.$store.commit('workflow/setActiveComment', false)
    }
}
</script>
