<template>
    <v-card>
        <v-card-title class="pr-0 pl-4 m-modal--title">
            {{project ? '  ' : '  '}}
            <v-spacer></v-spacer>
            <v-btn
                text
                icon
                @click="cancel"
                color="#7f7f7f">
                <v-icon size="20">close</v-icon>
            </v-btn>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="pt-5 px-4" ref="body">
            <v-row>
                <v-col cols="12" sm="6" md="6">
                    <v-form ref="form">
                        <v-text-field
                            label=" "
                            placeholder=" "
                            v-model="name"
                            required
                            :rules="check"
                        >
                            <template #label> 
                                <span class="red--text"><strong>* </strong></span>
                            </template>
                        </v-text-field>
                    </v-form>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                    <v-text-field
                        label=""
                        placeholder=" "
                        v-model="client"
                    ></v-text-field>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="12" sm="6" md="6">
                    <v-autocomplete
                        label="  "
                        placeholder=" "
                        :items="managers"
                        v-model="manager"
                        chips
                        item-text="name"
                        item-value="id"
                        >
                        <template v-slot:selection="data">
                            <v-chip
                                dark
                                v-bind="data.attrs"
                                :input-value="data.selected"
                                close
                                @click:close="removeChips()"
                            >
                                <v-avatar
                                    left
                                >
                                    <v-img :src="data.item.avatar"></v-img>
                                </v-avatar>
                                {{ data.item.name }}
                            </v-chip>
                        </template>

                        <template v-slot:item="data">
                            <v-list-item-avatar size="30">
                                <img :src="data.item.avatar">
                            </v-list-item-avatar>
                            <v-list-item-content>
                                <v-list-item-title v-html="data.item.name"></v-list-item-title>
                                <v-list-item-subtitle v-html="data.item.role"></v-list-item-subtitle>
                            </v-list-item-content>
                        </template>
                    </v-autocomplete>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                    <v-text-field
                        label=" "
                        placeholder=" "
                        v-model="number"
                        height="42"
                    ></v-text-field>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="12" sm="12" md="12">
                    <v-textarea
                        label=""
                        placeholder=" "
                        v-model="description"
                        multi-line
                        rows="3">
                    </v-textarea>
                </v-col>
            </v-row>

            <v-row >
                <v-col cols="12" sm="6" md="6"
                    v-for="(i, j) in addition"
                    :key="j"
                >
                    <v-text-field
                        v-if="i.type == 'string'"
                        placeholder=" "
                        :label="i.name"
                        v-model="i.value"
                    >
                        <template slot="append-outer">
                            <v-icon @click="editCustom(i.id)">edit</v-icon>
                        </template>
                    </v-text-field>
                    <v-text-field
                        v-else-if="i.type == 'number'"
                        placeholder=" "
                        :label="i.name"
                        v-model="i.value"
                        @input="onlyNumber($event, i)"
                    >
                        <template slot="append-outer">
                            <v-icon @click="editCustom(i.id)">edit</v-icon>
                        </template>
                    </v-text-field>
                    <v-autocomplete
                        v-else-if="i.type == 'list'"
                        v-model="i.params.value"
                        :items="i.params.list"
                        item-text="name"
                        item-value="value"
                        :label="i.name"
                        placeholder=" "
                        clearable
                        persistent-hint
                    >
                        <template slot="append-outer">
                            <v-icon @click="editCustom(i.id)">edit</v-icon>
                        </template></v-autocomplete>
                </v-col>
            </v-row>

        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="pa-5 px-4">
            <v-btn outlined color="normal" class="m-btn m-btn-normal" @click.stop="editCustom(null)"> </v-btn>
            <v-spacer></v-spacer>
            <v-btn outlined color="primary" class="m-btn" @click="validate">
                {{project ? '' : ''}}
            </v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'

export default {
    name: 'Edit',
    props: ['component', 'addition', 'additionID', 'project', 'open', 'storeage'],
    data () {
        return {
            check: [v => !!v || ' '],
            name: '',
            client: '',
            manager: null,
            description: '',
            number: '',
            disabledSave: true,
            selectCountry: null,
            selectRegion: null,
            selectCity: null,
            menu: false
        }
    },
    computed: {
        readySave(){
            let ready = false
            let name = this.name || false
            if(name){
                ready = true
            }
            return ready
        },
        managers(){
            let users = this.$store.state.administration.company.users || []
            let list = users.map(u => ({
                id: u.id,
                avatar: u.avatarSrc,
                name: u.first_name + ' ' + u.last_name,
                role: ''
            }))
            return list
        }
    },
    watch: {
        open(v){
            this.$refs['body'].scrollTop = 0
            if(v){
                document.addEventListener('keyup', this.enter, false)
            }
            else{
                document.removeEventListener('keyup', this.enter, false)
            }
        },
    },
    methods: {
        cancel(){
            this.disabledSave = true
            this.name = ''
            this.client = ''
            this.manager = null
            this.description = ''
            this.number = ''
            this.$emit('cancel')
        },
        removeChips(){
            this.manager = null
        },
        saveChange(){
            let body = {
                name: this.name,
                description: this.description,
                number: this.number,
                client: this.client,
                in_charge_id: this.manager ? this.manager + '' : this.manager,
                addition: this.addition.map(a => ({
                    name: a.name,
                    // value: JSON.stringify(a)
                    value: a.type == 'string'
                        ? a.value
                        : a.type == 'number'
                            ? parseInt(a.value)
                            : {
                                value: a.params.list.find(f => f.value == a.params.value).name,
                                list: a.params.list.map(p => p.name)
                            }
                })) || null
            }
            let type_id = 'c'
            let id = this.$store.state.administration.company.id
            if(this.project){
                let uuid = this.project.id
                this.$store.dispatch('workflow/updateProject', {type_id, id, uuid, body})
                .then(() => {
                    this.$notify({
                        group: 'note',
                        type: 'success',
                        text: ' ' + body.name + ' .'
                    })
                })
            }
            else{
                let uuid = uuidv4()
                this.$store.dispatch('workflow/addProject', {type_id, id, uuid, body})
                .then(() => {
                    this.$notify({
                        group: 'note',
                        type: 'success',
                        text: '  ' + body.name
                    })
                })
            }
            this.cancel()
        },
        validate(){
            if(this.$refs.form.validate()){
                this.saveChange()
            }
        },
        enter(e){
            if(e.keyCode === 13){
                e.preventDefault()
                this.validate()
            }
        },
        onlyNumber(e, o){
            this.$nextTick(() => {
                o.value = o.value.replace(/[^0-9]/g, '')
            })
        },
        editCustom(id){
            let keys = Object.keys(this.$data).filter(k => k != 'check')
            let storeage = {}
            keys.map(k => {
                storeage[k] = this[k]
            })
            this.$emit('update:storeage', storeage)
            this.$emit('update:component','custom')
            this.$emit('update:additionID', id)
        }
    },
    created(){
        if(this.storeage){
            let keys = Object.keys(this.storeage)
            keys.map(k => {
                this[k] = this.storeage[k]
            })
        }
        else if(this.project){
            let params = JSON.parse(JSON.stringify(this.project))
            this.name = params.info.name
            this.client = params.info.client
            this.number = params.info.number
            this.description = params.info.description
            this.manager = params.info.in_charge_id
        }
    }
}
</script>
