<template>
    <v-app-bar
        :color="bg_color"
        clipped-left
        clipped-right
        app
        class="m-toolbar"
        :flat="true">
        
        <promise/>

        <logobar :part="part"/>

        <v-toolbar-title class="pl-4">
            <span
                v-for="(link, i) in title">
                <router-link
                    :class="['m-title-link', {'m-title-link--disabled': title.length == i + 1}]"
                    :to="link.path">{{link.title}}</router-link>
                {{title.length != i + 1 ? '/' : ''}}
            </span>
        </v-toolbar-title>

        <div class="flex-grow-1"></div>

        <Profile/>

        <Menu/>
    </v-app-bar>
</template>

<script>
import { mapState } from "vuex"
import Logobar from '@/components/core/Logobar'
import Menu from '@/components/core/appbar/Menu'
import Profile from '@/components/core/appbar/Profile'
import Promise from '@/components/core/appbar/Promise'

export default {
    name: 'AppBar',
    props: ['part'],
    components: {
        Logobar,
        Promise,
        Menu,
        Profile
    },
    data(){
        return{
            bg_color: '#fbfbfb',
            bg_color_list: {
                normal: '#fbfbfb',
                white: '#fff'
            },
            logo: require('@/assets/images/Logo_icon.svg'),
            logo_text: require('@/assets/images/Logo_text.svg'),
            titlePage: '',
            backLink: null,
        }
    },
    watch: {
        '$route' (to, from) {
            this.setBG()
        }
    },
    computed: mapState({
        title(state){
            return state.toolbar.title
        }
    }),
    created(){
        this.setBG()
    },
    methods: {
        setBG(){
            if(this.$route.name == 'workflow-view'){
                this.bg_color = this.bg_color_list.white
            }
            else{
                this.bg_color = this.bg_color_list.normal
            }
        }
    }
}
</script>
