<template>
    <v-container fluid px-3 pb-10 pt-0 class="m-container">

        <v-toolbar flat height="auto" class="m-table-toolbar">
            <v-text-field
                v-model="search"
                prepend-icon="search"
                placeholder="..."
                single-line
                hide-details
                class="m-table-toolbar--search ml-4"
                ></v-text-field>

            <v-spacer></v-spacer>
            <v-btn
                text
                @click.stop="addUser"
                class="ma-0 m-btn--text_icon">
                <v-icon
                    size="18"
                    left>mdi-account-plus</v-icon> 
            </v-btn>
        </v-toolbar>

        <v-data-table
            :headers="headers"
            :items="employees"
            :show-select="getRole == '1' || getRole == '2'"
            v-model="selected"
            @toggle-select-all="selectRow"
            :search="search"
            disable-pagination
            hide-default-footer
            class="m-table-custom"
        >
            <template v-slot:no-data>
                {{noData}}
            </template>

            <template v-slot:item.data-table-select="{ item, isSelected, select }">
                <v-simple-checkbox
                    :value="isSelected"
                    @input="select($event)">
                </v-simple-checkbox>
            </template>

            <template v-slot:item.src="{ item }">
                <v-avatar
                    size="30"
                    >
                    <img
                        class="m-card--avatar-wrap_img"
                        :src="getAvatar(item)"
                        alt="avatar"
                    >
                </v-avatar>
            </template>
            <template v-slot:item.action="{ item }">
                <action-menu
                    v-if="(item.status && (getRole == '1' || getRole == '2')) || !item.status"
                    @editUser="editUser"
                    @reconfirm="reconfirm"
                    @removeUser="removeUser"
                    :item="item"
                />
            </template>
            <template v-slot:item.status="{ item }">
                <span
                    :class="!item.status ? 'red--text' : 'green--text'">
                    {{getStatus(item.status)}}
                </span>
            </template>
        </v-data-table>

        <v-card
            class="m-table-custom--action"
            :style="cardActionStyle"
            v-show="selected.length"
            >
            <v-card-actions>
                <v-btn
                    text
                    color="primary"
                    class="m-btn"
                    @click="removeUser()">
                    <v-icon size="20" class="mr-1" color="#0070e0">mdi-trash-can-outline</v-icon>
                    
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-container>
</template>

<script>
import ActionMenu from "./ActionMenu"
import { createAvatarChar } from '@/utils/services'

export default {
    name: 'EmployeesList',
    components: {
        ActionMenu,
    },
    data(){
        return {
            selected: [],
            headers: [
                {
                    text: '',
                    align: 'center',
                    sortable: false,
                    value: 'src',
                },
                { text: '', value: 'email' },
                { text: '', value: 'name' },
                /*
                { text: ' ', value: 'last_activity' },
                { text: '', value: 'department' },
                */
                { text: '', value: 'position' },
                { text: '', value: 'status' },
                { text: '', value: 'action', sortable: false },
            ],
            search: '',
            noData: ' ',
        }
    },
    computed: {
        employees: function(){
            let state = this.$store.state.administration
            let list = state.invited
            list = list ? list : []
            let users = state.company.users || []

            users = users.map(u => {
                return {...u,
                    src: u.avatarSrc,
                    name: u.first_name + ' ' + u.last_name,
                    status: u.status,
                    position: Object.prototype.hasOwnProperty.call(u, 'profession')
                        ? u.profession
                        : ''
                }
            })

            list = list.filter(l => !l.status).map((l, j) => {
                return {...l,
                    id: 'id-' + j,
                    src: l.email.slice(0, 1),
                    position: ''
                }
            })

            // let owner = users.filter(u => list.every(l => l.email != u.email))
            //
            // owner = owner.length ? [{...owner[0],
            //     email: owner[0].email,
            //     src: owner[0].avatar,
            //     name: owner[0].first_name + ' ' + owner[0].last_name,
            //     status: owner[0].status,
            //     position: Object.prototype.hasOwnProperty.call(owner[0], 'profession')
            //         ? owner[0].profession
            //         : ''
            // }] : []

            list = [...users, ...list]
            return list
        },
        getRole(){
            return this.$store.state.administration.user.role
        },
        avatar(){
            return this.$store.state.administration.userAvatar
        },
        cardActionStyle(){
            if(this.selected.length){
                this.widthEl = this.$el.clientWidth
            }
            return {width: this.widthEl + 'px'}
        }
    },
    methods: {
        selectRow(e){
            let list = e.items
            if(this.selected.length != list.length){
                this.selected = list
            }
            else{
                this.selected = []
            }
        },
        getStatus(v){
            let status = ''
            switch(v){
                case 0: status = ''
                        break
                case 1:
                case 200: status = ''
                        break
                default: status = ''
            }
            return status
        },
        getAvatar(i){
            if(!i.src || i.src.length == 1){
                let name = i.name ? i.name : i.email
                return createAvatarChar(name)
            }
            else{
                return i.src
            }
        },
        addUser(){
            this.$router.push({name: 'employees_invite'})
        },
        editUser(i){
            this.$router.push({name: 'employee_account', params: {id: i.id, role: this.getRole}})
            .catch(failure => {
                console.log('failure')
            })
        },
        reconfirm(i){
            this.$router.push({name: 'employees_invite',  query: {email: i.email}})
        },
        removeUser(users = []){
            let promises = []
            let list = [...this.selected, ...users]
            list.map(u => {
                if(u.status){
                    promises.push(this.$store.dispatch('administration/invitedRemove', u.id))
                }
                else{
                    promises.push(this.$store.dispatch('administration/noregistredRemove', u.email))
                }
            })
            Promise.all(promises)
            .then(res => {
                list.map(u => {
                    this.$notify({
                        group: 'note',
                        type: 'success',
                        text: ' ' + u.email
                    })
                })
                this.selected = []
                this.$store.dispatch('administration/invitedList')
                this.$store.dispatch('administration/getCompany')
            })
        }
    },
    created(){
        this.$store.dispatch('administration/invitedList')
    }
}
</script>
