<template>
    <component
        :is="getViewer"
        :model="model"
        :list="list"
        :doc="doc"
        :project_id="id"
    />
</template>

<script>
import ViewPdf from "./dctview/ViewPdf"
import ViewObj from "./dctview/ViewObj"
// import ViewObj from "./dctview/ViewObj_worker"
import ViewDwg from "./dctview/ViewDwg"
import ViewDxf from "./dctview/ViewDxf"

export default {
    name: 'DctView',
    components: {
        ViewPdf,
        ViewObj,
        ViewDwg,
        ViewDxf
    },
    props: ['id', 'doc', 'list'],
    data(){
        return {
            model: {
                type: ''
            },
            permittedTypes: ['obj', 'pdf', 'dwg', 'dxf']
        }
    },
    computed: {
        getViewer(){
            let state = this.$store.state.workflow
            let type = state.fileTypeConversion.some(s => s == this.model.type)
                ? 'obj'
                : this.model.type == 'consolidations'
                    ? 'obj'
                    : this.model.type
            let component = this.permittedTypes.includes(type)
                ? 'view-' + type
                : false
            return component
        }
    },
    methods: {
        getModel(){
            let list = this.$store.state.workflow.listDocs
            let id = this.doc || ''
            if(!list.length){
                this.$store.dispatch('workflow/getFolders')
                .then(() => {
                    return this.$store.dispatch('workflow/getGroups')
                })
                .then(res => {
                    return this.$store.dispatch('workflow/createFoldersMap')
                })
                .then(() => {
                    list = this.$store.state.workflow.listDocs
                    let file = list.filter(l => l.id == id)
                    if(file.length){
                        this.model = file[0]
                    }
                    let name = this.model.name || 'name'
                    this.$store.commit('toolbar/setDctView', {name: name, status: ''})
                })
            }
            else{
                let file = list.filter(l => l.id == id)
                if(file.length){
                    this.model = file[0]
                    let name = this.model.name || 'name'
                    this.$store.commit('toolbar/setDctView', {name: name, status: ''})
                }
            }
        },
        // getModelList(){
        //     this.model.type = 'obj'
        //     let list = this.list
        //     if(!list){
        //         list = this.$route.query
        //         if(list){
        //             let listDocs = this.$store.state.workflow.listDocs
        //             list = Object.entries(list).map(([k, v]) => {
        //                 return listDocs.find(f => f.id == v)
        //             })
        //         }
        //         else{
        //             list = []
        //         }
        //     }
        // },
        checkInit(){
            if(!this.$store.state.workflow.activeProject){
                let project = {id: this.$route.params.id}
                return new Promise(resolve => {
                    Promise.all([
                        this.$store.commit('workflow/setActiveProject', project),
                        this.$store.commit('workflow/setExpanded', []),
                        this.$store.commit('workflow/setCurrentFolder', 0),
                        this.$store.dispatch('workflow/getFolders'),
                        this.$store.dispatch('workflow/getGroups'),
                        this.$store.dispatch('administration/getFoldersPermissions'),
                        this.$store.dispatch('administration/getFilesPermissions')
                    ])
                    .finally(res => {
                        resolve(this.$store.dispatch('workflow/createFoldersMap'))
                    })
                })
            }
            else{
                return Promise.resolve(true)
            }
        }
    },
    created(){
        this.checkInit()
        .then(() => {
            this.getModel()
            // if(this.doc == 'consolidation'){
            //     this.getModelList()
            // }
            // else{
            //     this.getModel()
            // }
        })
    }
}
</script>
