        <v-divider></v-divider>
        <v-card-actions class="pa-5 px-4">
            <v-spacer></v-spacer>
            <v-btn outlined color="normal" class="m-btn m-btn-normal" @click.stop="cancel"></v-btn>
            <v-btn outlined color="primary" class="m-btn" @click="setComponent">
            </v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>

export default {
    name: 'RulesTypes',
    props: ['temp'],
    data(){
        return {
            type: '',
            selected: [],
            parts: {
                geom: 'RulesSettings',
                params: 'RulesSettingsParams',
                repeat: 'RulesSettingsRepeat'
            }
        }
    },
    methods: {
        onChange(v){
            this.type = v.toString()
        },
        cancel(){
            this.$emit('cancel')
        },
        setComponent(){
            let update = JSON.parse(JSON.stringify(this.temp))
            update = {...this.temp, part: this.type}
            this.$emit('setParams', update)
            this.$emit('setComponent', this.parts[this.type])
        },
        enter(e){
            if(e.keyCode === 13){
                e.preventDefault()
                this.cancel()
            }
        },
        mounted(){
            document.addEventListener('keyup', this.enter, false)
        },
        beforeDestroy(){
            document.removeEventListener('keyup', this.enter, false)
        }
    }
}
</script>
