<template>
    <v-card
        :outlined="true"
        color="#fafafa"
        class="m-comment-card"
        :class="[{active: activeComment}]"
        @click.native="view"
        @mouseenter="commentHover"
        @mouseleave="commentLeave"
        v-click-outside="no_view"
    >
        <comment-item :comment="comment" :viewMode="viewMode"/>
        <v-container v-show="viewMode" class="m-reply-wrap pt-0">
            <v-textarea
                v-model="reply"
                class="m-comment-avatar"
                placeholder=""
                auto-grow
                hide-details
                multi-line
                rows="2">
                <template v-slot:prepend-inner>
                    <v-avatar
                        size="30">
                        <img
                            src="https://cdn.vuetifyjs.com/images/john.jpg"
                            alt="avatar"
                        >
                    </v-avatar>
                </template>
            </v-textarea>
            <v-row >
                <v-col cols="2" class="pt-0">
                </v-col>
                <v-col cols="10" class="pt-0">
                    <v-card-actions class="pa-0">
                        <v-btn
                            :disabled="!reply"
                            class="mt-2 m-btn"
                            outlined
                            dense
                            color="primary"
                            @click="replyComment"
                        >
                        
                        </v-btn>
                        <v-btn
                            :disabled="!reply"
                            class="mt-2 m-btn m-btn-normal"
                            outlined
                            dense
                            color="normal"
                            @click="clearReply"
                        >
                        
                        </v-btn>
                    </v-card-actions>
                </v-col>
            </v-row>
        </v-container>
    </v-card>
</template>

<script>
// ///v-click-outside="no_view"
//import ClickOutside from '@/plugins/click_outside'
import vClickOutside from 'v-click-outside'
import CommentItem from './CommentItem'

    export default {
        name: 'Comment',
        props: ['comment'],
        directives: {
            clickOutside: vClickOutside.directive
        },
        components: {
            CommentItem
        },
        data () {
            return {
                reply: '',
                viewMode: false
            }
        },
        computed: {
            draftSelected(){
    			return this.$store.state.workflow.draftSelected
            },
            activeComment(){
                let active = this.$store.state.workflow.activeComment
                if(active == this.comment.id){
                    this.viewMode = true
                    //this.$store.commit('workflow/setDraftSelected', this.comment.id)
                }
                else{
                    this.viewMode = false
                }
    			return this.viewMode
            }
        },
        watch: {
            viewMode(v){
                if(v){
                    this.$emit('update:posActive', this.$el.offsetTop)
                }
            }
        },
        methods: {
            view(){
                this.$emit('getPosCamera', this.comment.position)
                this.$store.commit('workflow/setActiveComment', this.comment.id)
                //    this.viewMode = true
                //    this.$store.commit('workflow/setDraftSelected', this.comment.id)
            },
            no_view(){
                if(this.viewMode){
                    this.$store.commit('workflow/setActiveComment', false)
                    this.viewMode = false
                }
                //this.viewMode = false
            },
            replyComment(){
                let reply = {
                    type: 'reply',
                    comment_id: this.comment.id,
                    avatar: 'https://cdn.vuetifyjs.com/images/john.jpg',
                    user: ' ',
                    text: this.reply,
                    title:"  1"
                }
                this.$store.dispatch('workflow/addReply', {
                    reply: reply,
                    id: this.comment.id
                })
                this.reply = ''
            },
            clearReply(){
                this.reply = ''
            },
            commentHover(){
                this.$store.commit('workflow/setDraftHover', this.comment.id)
            },
            commentLeave(){
                this.$store.commit('workflow/setDraftHover', '')
            },
        }
    }
</script>
