<template>
    <v-card
        class="cursor-move"
        color="#fff">
        <div class="d-flex">
            <v-card-title class="m-text--size16 m-text--color-4a5568 pb-0">
                {{item.name}}
            </v-card-title>
            <v-spacer />
            <v-avatar
                class="ma-4 mb-0"
                size="30">
                <img
                    :src="item.avatar"
                >
            </v-avatar>
        </div>
        <v-card-subtitle class="pt-1">{{item.dname}}</v-card-subtitle>

        <v-divider></v-divider>
        <div class="py-0 px-4">
            <v-img
                :src="item.src"
                height="200px"
            ></v-img>
        </div>

        <v-divider></v-divider>
        <v-card-actions class="py-3 pr-2 pl-0 pb-0">
            <div>
                <v-card-subtitle class="py-0"> {{getDate(item.create)}}
                </v-card-subtitle>
                <v-card-subtitle class="py-0">
                    {{getDate(item.deadline)}}
                </v-card-subtitle>
            </div>
            <v-spacer></v-spacer>

        </v-card-actions>
        <v-card-actions class="py-3 pr-2 pl-0 pt-0">
            <v-spacer></v-spacer>
            <v-btn
                text
                color="primary"
                class="m-btn"
                @click="editTask(item)"
            >
            {{checkOwner(item.owner) ? '' : ''}}
            </v-btn>

        </v-card-actions>

    </v-card>
</template>

<script>
export default {
    name: 'Task',
    props: ['item', 'editItem'],
    data(){
        return {
        }
    },
    computed: {
        getOwner(){
            return this.$store.state.administration.user.id
        }
    },
    methods: {
        checkOwner(v){
            return this.getOwner == v
        },
        editTask(i){
            this.$emit('update:editItem', i)
        },
        getDate(d){
            let options = {year: '2-digit', month: 'short', day: '2-digit'}
            return (new Date(d)).toLocaleDateString("ru-RU", options)
        },
    }
}
</script>

<style scoped>

.cursor-move {
    cursor: move;
}
</style>
