<template>
    <v-navigation-drawer
        permanent
        :width="tools_width"
        class="m-navigator-tools m-navigator-no-scroll px-2 pr-0 m-navigator-hide"
        right
        clipped
        color="transparent"
        app>
            <v-layout class="ma-0" fill-height>
                <div
                    class="m-overflow-hidden m-tools-content"
                    :class="{'m-tools-content--mini': mini}">
                    <!--<v-row>-->
                    <v-layout class="ma-0" fill-height>
                        <comments3d
                            v-if="tab == 'comment'"
                            :getPosCamera.sync="getPosCamera"
                            :setPosCamera="setPosCamera"
                            :documentId="documentId"/>
                        <history-list v-if="tab == 'history'"/>
                        <access v-if="tab == 'access'"/>
                    </v-layout>
                </div>
                <div ccols="2" class="pl-0 pr-0 text-right m-tools-menu">
                    <v-list-item
                        @click.stop="expand"
                        class="m-nav-right-tools m-nav-right-tools__dil"
                        >
                        <v-icon
                            size="25"
                            color="#7f7f7f">{{mini ? 'mdi-chevron-double-left' : 'mdi-chevron-double-right'}}</v-icon>
                    </v-list-item>
                    <v-list-item
                        class="m-nav-right-tools"
                        v-for="(i, j) in tabs"
                        :key="j"
                        :class="{active: i.name == tab}"
                        @click="setTab(i.name)"
                        >
                        <v-icon
                            :color="activeBtn(i.name)"
                            size="25">{{i.icon}}</v-icon>
                    </v-list-item>
                </div>
            </v-layout>
    </v-navigation-drawer>
</template>

<script>
import Comments3d from "./Comments3d"
import HistoryList from "./HistoryList"
import Access from "./Access"
    export default {
        name: 'Tools',
        components: {
            Comments3d,
            HistoryList,
            Access
        },
        props: ['getPosCamera', 'setPosCamera', 'documentId'],
        data(){
            return {
                mini: true,
                tools_width: 0,//56//440,
                tab: 'comment',
                tabs: [
                    {
                        name: 'history',
                        icon: 'history'
                    },{
                        name: 'comment',
                        icon: 'mdi-comment-text-outline'
                    },{
                        name: 'access',
                        icon: 'mdi-stamper'
                    }
                ]
            }
        },
        watch: {
            mini: function(v){
                this.tools_width = v ? 0 : 440
                this.$emit('update:toolsbar', v)
            }
        },
        methods: {
            setTab(v){
                this.tab = v
            },
            activeBtn(v){
                let color = this.tab == v ? '#2c2c2c' : '#7f7f7f'
                return color
            },
            expand(){
                this.mini = !this.mini
            }
        },
    }
</script>
