<template>
    <v-card
        class="m-card-scrollable"
        :class="{'m-panel__hidden': minimize}"
        :style="styleP"
        >
        <v-card-title class="pr-0 pl-4 py-1 m-modal--title-size14 m-card-scrollable__title"> 
            <v-spacer></v-spacer>
            <v-btn
                text
                icon
                @click="minimize = !minimize"
                color="#7f7f7f">
                <v-icon size="20">
                    {{!minimize ? 'mdi-window-minimize' : 'mdi-window-maximize'}}
                </v-icon>
            </v-btn>
            <v-btn
                text
                icon
                @click="cancel"
                color="#7f7f7f">
                <v-icon size="20">close</v-icon>
            </v-btn>
        </v-card-title>

        <v-divider></v-divider>

        <v-card-title class="pa-0">
            <div class="pr-0 pl-4 py-1 m-panel__collision--options">
                <icon-botton
                    class="mr-4"
                    :disabled="!compute_ready"
                    @onClick="component = 'RulesSelect'"
                    >trd-template
                </icon-botton>
                <icon-botton
                    class="mr-4"
                    :disabled="!compute_ready"
                    @onClick="component = 'GroupAdd'"
                    >trd-group-add
                </icon-botton>
                <icon-botton
                    class="mr-4"
                    :disabled="!checkGroups || !compute_ready"
                    @onClick="removeItem"
                    >trd-group-delete
                </icon-botton>
                <icon-botton
                    class="mr-4"
                    :disabled="!compute_ready"
                    @onClick="component = 'RulesTypes'"
                    >trd-rules-add
                </icon-botton>
                <icon-botton
                    class="mr-4"
                    :disabled="!checkRules || !compute_ready"
                    @onClick="removeItem"
                    >trd-rules-delete
                </icon-botton>
                <icon-botton
                    class="mr-4"
                    :disabled="!(checkGroups || checkRules) || !compute_ready"
                    @onClick="editItem"
                    >trd-edit
                </icon-botton>
                <v-spacer></v-spacer>
                <v-btn
                    text
                    small
                    tile
                    class="m-btn"
                    color="primary"
                    :disabled="!checkRules || !compute_ready"
                    @click="onComputeCollisions"
                    >
                    <v-icon left size="20">
                        trd-checked
                    </v-icon>
                    
                </v-btn>
            </div>
        </v-card-title>

        <v-divider class="pb-2"></v-divider>

        <v-card-text
            class="pa-0 pb-5 m-card-scrollable__text">

            <v-skeleton-loader
                v-show="!compute_ready"
                type="list-item-three-line, list-item-three-line"
            ></v-skeleton-loader>

            <v-treeview
                v-show="compute_ready"
                :key="change"
                :active.sync="selected"
                :items="getGroups"
                return-object
                class="m-panel__collision--tree m-text--size12"
                dense
                activatable
                expand-icon="mdi-chevron-down"
                transition
                item-key="id"
                >
                <template v-slot:prepend="{item}">
                    <v-icon size="20">
                        {{'trd-' + item.type}}
                    </v-icon>
                </template>
                <template v-slot:append="{item}">
                    <template v-if="item.status != -1">
                        <v-icon v-if="!item.status" size="20" color="error">
                            mdi-alert-outline
                        </v-icon>
                        <v-icon v-else-if="item.status" size="20" color="success">
                            trd-checked-ok
                        </v-icon>
                    </template>
                </template>
            </v-treeview>
        </v-card-text>

        <dialog-view
            :component.sync="component"
            :temp.sync="temp"
            :model="model"
            :componentNames.sync="componentNames"
            :source="source"
            :elementParameters.sync="elementParameters"
            @updateTemp="updateTemp"
            @saveMeta="saveMeta"
            @cancel="hideDialog"
            />

    </v-card>
</template>

<script>
import IconBotton from '@/components/custom/IconBotton'
import DialogView from './panel_collision_settings/DialogView'

export default {
    name: 'PanelCollisionSettings',
    props: ['menuItem', 'collision_settings', 'collisions_compiling', 'styleP', 'model', 'source', 'compute_ready'],
    components: {
        IconBotton,
        DialogView
    },
    data(){
        return {
            minimize: false,
            component: false,
            elementParameters: [],
            temp: {},
            change: 0,
            componentNames: null,
            selected: [],
            compilingQueue: {
                active: 0,
                group: 0,
                rule: 0
            }
        }
    },
    computed: {
        checkGroups(){
            return this.selected.some(s => s.type == 'group')
        },
        checkRules(){
            return this.selected.some(s => s.type == 'rules')
        },
        getGroups(){
            let meta = this.$store.state.common.meta
            let collisions = meta.collisions
            let groups = collisions ? collisions.groups || [] : []
            return groups
        }
    },
    watch: {
        collision_settings(v){
            if(v && !this.componentNames){
                if(this.model.type == 'consolidations'){
                    let prmslist = []
                    this.model.consolidations.map(c => {
                        let doc = this.$store.state.workflow.listDocs.find(f => f.id == c)
                        let url = ''
                        if(doc.version == null){
                            url = doc.files[0].url
                        }
                        else{
                            let file = doc.files.find(i => i.version == doc.version)
                            url = file ? file.url : doc.files[0].url
                        }
                        prmslist.push(this.$store.dispatch('common/getFileInfo', {id: url + '/elems.json'}))
                    })
                    Promise.all(prmslist)
                    .then(res => {
                        let elems = {bim: {}, ksi: {}}
                        res.map(r => {
                            let keys = Object.keys(r.bim).map(k => {
                                if(elems.bim[k]){
                                    elems.bim[k] = [...elems.bim[k], ...r.bim[k]]
                                }
                                else{
                                    elems.bim[k] = r.bim[k]
                                }
                            })
                            keys = Object.keys(r.ksi).map(k => {
                                if(elems.ksi[k]){
                                    elems.ksi[k] = [...elems.ksi[k], ...r.ksi[k]]
                                }
                                else{
                                    elems.ksi[k] = r.ksi[k]
                                }
                            })
                        })
                        this.componentNames = elems
                    })
                    .catch(er => {
                        console.log('Get tag elems.json error:', er)
                    })

                    // this.model.consolidations.map(c => {
                    //     prmslist.push(this.$store.dispatch('common/getFileInfo', {id: c + '/elems.json'}))
                    // })
                    // Promise.all(prmslist)
                    // .then(res => {
                    //     console.log('>>>>res',res)
                    // })
                }
                else{
                    if(this.model.tags['elems.json'] == 1){
                        let url = ''
                        if(this.model.version == null){
                            url = this.model.files[0].url
                        }
                        else{
                            let file = this.model.files.find(i => i.version == this.model.version)
                            url = file ? file.url : this.model.files[0].url
                        }
                        this.$store.dispatch('common/getFileInfo', {id: url + '/elems.json'})
                        .then(res => {
                            this.componentNames = res
                        })
                        .catch(er => {
                            console.log('Get tag elems.json error:', er)
                        })
                    }
                }
            }
        },
        collisions_compiling(v){
            if(v){
                if(!this.compilingQueue.active){
                    this.compilingQueue.active = 1
                    this.compilingQueue.group = this.getGroups.length
                    this.compilingQueue.rule = this.getGroups[0].children.length
                    this.activeAutoCompiling(false)
                }
                else{
                    if(this.compilingQueue.rule != 0){
                        this.compilingQueue.rule -= 1
                    }
                    else if(this.compilingQueue.group != 0){
                        this.compilingQueue.group -= 1
                        let ind = this.getGroups.length - this.compilingQueue.group
                        this.compilingQueue.rule = this.getGroups[ind].children.length
                    }
                    else{
                        this.compilingQueue.active = 0
                        this.activeAutoCompiling(false)
                    }
                    let g = this.getGroups.length - this.compilingQueue.group
                    let r = this.getGroups[g].children.length - this.compilingQueue.rule
                    let item = this.getGroups[g].children[r]
                    this.computeRule(item)
                }
            }
            else{
                if(this.compilingQueue.active){
                    this.activeAutoCompiling(true)
                }
            }
        }
    },
    methods: {
        hideDialog(){
            this.temp = {}
            this.component = false
        },
        cancel(){
            this.temp = {}
            this.$emit('setOption', this.menuItem)
        },
        activeAutoCompiling(v){
            this.$emit('update:collisions_compiling', v)
        },
        onComputeCollisions(){
            this.computeRule()
        },
        getElementIdList(list, std = 'ksi'){
            let ids = []
            list.map(l => {
                ids = [...ids, ...this.componentNames[std][l]]

                // let elem = this.componentNames[std].find(f => f.name == l)
                // if(elem){
                //     ids = [...ids, ...elem.list]
                // }
            })
            return ids
        },
        computeRule(item = null){
            if(!this.selected.length && !item){
                return
            }
            item = item
                ? item
                : this.selected.length
                    ? this.selected[0]
                    : {part: 'none'}
            this.getGroups.map(g => {
                g.children.map(r => {
                    if(r.id == item.id){
                        item = r
                    }
                })
            })
            if(!item){
                return
            }
            if(item.part == 'geom'){
                let data = {
                    list_1: this.getElementIdList(item.list_1, item.std),
                    list_2: this.getElementIdList(item.list_2, item.std),
                    group: item.parent,
                    rule: item.id,
                    part: item.part
                }
                this.$emit('onComputeCollisions', data)
            }
            if(item.part == 'params'){
                let group = this.getGroups.find(f => f.id == item.parent)
                let elems = group.children.find(c => c.id == item.id).elements
                let data = {
                    elements: elems
                        .filter(f => Object.keys(this.componentNames[item.std]).some(s => s == f.name))
                        .map(e => {
                            return {
                                ...e,
                                list: this.getElementIdList([e.name], item.std)
                            }
                    }),
                    group: item.parent,
                    std: item.std,
                    rule: item.id,
                    part: item.part
                }
                this.$emit('onComputeParameters', data)
            }
            if(item.part == 'repeat'){
                let data = {
                    group: item.parent,
                    rule: item.id,
                    part: item.part
                }
                this.$emit('onComputeRepeat', data)
            }
        },
        editItem(){
            let item = this.selected[0]
            if(item.type == 'rules'){
                if(this.selected[0].part == 'geom'){
                    this.temp.group = this.getGroups.find(f => f.id == item.parent)
                    this.temp.ruleName = item.name
                    this.temp.std = item.std
                    this.temp.desc = item.desc
                    this.temp.list_1 = item.list_1
                    this.temp.list_2 = item.list_2
                    this.temp.edit = item.id

                    this.component = 'RulesSettings'
                }
                if(this.selected[0].part == 'params'){

                    this.temp.group = this.getGroups.find(f => f.id == item.parent)
                    let elems = this.temp.group.children.find(c => c.id == item.id).elements
                    this.temp.ruleName = item.name
                    this.temp.std = item.std
                    this.temp.desc = item.desc
                    this.temp.elements = elems
                    this.temp.edit = item.id
                    this.component = 'RulesSettingsParams'
                }
                if(this.selected[0].part == 'repeat'){

                    this.temp.group = this.getGroups.find(f => f.id == item.parent)
                    let elems = this.temp.group.children.find(c => c.id == item.id).elements
                    this.temp.ruleName = item.name
                    this.temp.desc = item.desc
                    this.temp.edit = item.id

                    this.component = 'RulesSettingsRepeat'
                }
            }
            else{
                this.temp.edit = item.id
                this.temp.editName = item.name

                this.component = 'GroupAdd'
            }
        },
        removeItem(){
            let item = this.selected[0]
            if(!item){
                return
            }
            if(item.type == 'group'){
                this.saveMeta({groups: this.getGroups.filter(f => f.id != item.id)})
            }
            else{
                this.saveMeta({groups: this.getGroups.map(g => {
                    if(g.id == item.parent){
                        g.children = g.children.filter(f => f.id != item.id)
                    }
                    if(!g.children.length){
                        g.status = -1
                    }
                    return g
                })})
            }
            this.selected = []
        },
        updateTemp(v){
            this.temp = v
        },
        saveMeta(v){
            let meta = JSON.parse(JSON.stringify(this.$store.state.common.meta))
            if(meta.collisions){
                meta.collisions = {...meta.collisions, ...v, version: this.model.version}
            }
            else{
                meta.collisions = {...v, version: this.model.version}
            }

            this.$store.dispatch('common/setFileMeta', {
                id: this.model.id,
                body: JSON.stringify(meta)
            })
            .then(res => {
                return  this.$store.dispatch('common/getFileMeta', {
                    id: this.model.id
                })
            })
            .then(res => {
                if(res.error){
                    this.$notify({
                        group: 'note',
                        type: 'error',
                        text: res.data.error
                    })
                }
                else{
                    // this.change += 1
                    this.$notify({
                        group: 'note',
                        type: 'success',
                        text: ' .'
                    })
                }
            })
        }
    }
}

</script>
