<template>
    <v-card>
        <v-card-title class="pr-0 pl-4 m-modal--title">  
            <v-spacer></v-spacer>
            <v-btn
                text
                icon
                @click="cancel"
                color="#7f7f7f">
                <v-icon size="20">close</v-icon>
            </v-btn>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text style="background-color: #fbfbfb; min-height: 120px;" class="pt-5 px-4">
            <v-text-field
                label=" "
                v-model="custom_name"
                :autocomplete="'off'"
            ></v-text-field>
            <v-select
                v-show="!additionID"
                v-model="custom_type"
                :items="types"
                item-text="name"
                item-value="value"
                persistent-hint
                label=""
            ></v-select>

            <v-text-field
                v-if="custom_type && custom_type == 'string'"
                label=""
                v-model="custom_value"
                :autocomplete="'off'"
            ></v-text-field>
            <v-text-field
                v-else-if="custom_type && custom_type == 'number'"
                label=""
                v-model="custom_value"
                @input="onlyNumber($event)"
                :autocomplete="'off'"
            ></v-text-field>
            <v-combobox
                v-else-if="custom_type && custom_type == 'list'"
                v-model="model"
                :filter="filter"
                :hide-no-data="!search"
                :items="items"
                :search-input.sync="search"
                :autocomplete="'off'"
                hide-selected
                label=" "
                multiple
                small-chips
            >
                <template v-slot:no-data>
                    <v-list-item>
                        <span class="subheading">   </span>
                        <v-chip
                            :color="`${colors[nonce - 1]} lighten-3`"
                            label
                            small
                        >
                        {{ search }}
                        </v-chip>
                    </v-list-item>
                </template>
                <template v-slot:selection="{ attrs, item, parent, selected }">
                    <v-chip
                        v-if="item === Object(item)"
                        v-bind="attrs"
                        :color="`${item.color} lighten-3`"
                        :input-value="selected"
                        label
                        small
                    >
                        <span class="pr-2">
                            {{ item.text }}
                        </span>
                        <v-icon
                            small
                            @click="parent.selectItem(item)"
                        >close</v-icon>
                    </v-chip>
                </template>
                <template v-slot:item="{ index, item }">
                    <v-text-field
                        v-if="editing === item"
                        v-model="editing.text"
                        autofocus
                        flat
                        background-color="transparent"
                        hide-details
                        solo
                        @keyup.enter="edit(index, item)"
                    ></v-text-field>
                    <v-chip
                        v-else
                        :color="`${item.color} lighten-3`"
                        dark
                        label
                        small
                    >
                        {{ item.text }}
                    </v-chip>
                    <div class="flex-grow-1"></div>
                    <v-list-item-action @click.stop>
                        <v-btn
                            icon
                            @click.stop.prevent="edit(index, item)"
                        >
                            <v-icon>{{ editing !== item ? 'mdi-pencil' : 'mdi-check' }}</v-icon>
                        </v-btn>
                    </v-list-item-action>
                </template>
            </v-combobox>
        </v-card-text>

        <v-divider></v-divider>
        <v-card-actions class="pa-5 px-4">
            <v-btn
                outlined
                color="primary"
                class="m-btn"
                v-if="additionID"
                @click="removeCustom"></v-btn>
            <v-spacer></v-spacer>
            <v-btn
                v-if="!additionID"
                outlined
                color="primary"
                class="m-btn"
                :disabled="checkChange()"
                @click="addCustom"></v-btn>
            <v-btn
                v-if="additionID"
                outlined
                color="primary"
                class="m-btn"
                :disabled="checkEditChange()"
                @click="addCustom"></v-btn>
            <v-btn outlined color="#2c2c2c" class="m-btn m-btn-normal" @click="cancel"></v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>
export default {
    props: ['addition', 'additionID'],
    name: 'Custom',
        data () {
            return {
                custom_name: '',
                custom_value: '',
                custom_type: null,

                types: [{
                    value: 'string',
                    name: ''
                },{
                    value: 'number',
                    name: ''
                },{
                    value: 'list',
                    name: ''
                }],

                colors: [],
                editing: null,
                index: -1,
                items: [
                    {header: '   '},
                ],
                nonce: 1,
                menu: false,
                model: [],
                x: 0,
                search: null,
                y: 0,
        }
    },
    watch: {
        model(val, prev){
            if (val.length === prev.length) return

            this.model = val.map(v => {
                if (typeof v === 'string') {
                    v = {
                        text: v,
                        color: this.colors[this.nonce - 1],
                    }
                    this.items.push(v)
                    this.nonce++
                }

            return v
            })
        },
    },
    methods: {
        onlyNumber(e){
            this.$nextTick(() => {
                this.custom_value = this.custom_value.replace(/[^0-9]/g, '')
            })
        },
        checkChange(){
            if(this.custom_type == 'string' || this.custom_type == 'number'){
                return !(this.custom_type && this.custom_name && this.custom_value)
            }
            else if(this.custom_type == 'list'){
                return !(this.custom_type && this.custom_name && this.model.length)
            }
            else{
                return true
            }
        },
        checkEditChange(){
            let item = this.addition.find(a => a.id == this.additionID)
            if(this.custom_type == 'string' || this.custom_type == 'number'){
                return !((this.custom_name != item.name)
                    || (this.custom_value != item.value))
            }
            else if(this.custom_type == 'list'){
                if(this.model.length != item.params.list.length){
                    return false
                }
                else{
                    for(let i = 0, len = this.model.length; i < len; i++){
                        if(this.model[i].text != item.params.list[i].name){
                            return false
                        }
                    }
                    return true
                }
            }
            else{
                return true
            }
        },
        edit(index, item){
            if(!this.editing){
                this.editing = item
                this.index = index
            }
            else{
                this.editing = null
                this.index = -1
            }
        },
        filter(item, queryText, itemText){
            if (item.header) return false

            const hasValue = val => val != null ? val : ''

            const text = hasValue(itemText)
            const query = hasValue(queryText)

            return text.toString()
                .toLowerCase()
                .indexOf(query.toString().toLowerCase()) > -1
        },
        removeCustom(){
            let addition = this.addition.filter(a => a.id != this.additionID)
            this.$emit('update:addition', addition)
            this.cancel()
        },
        addCustom(){
            let custom = {}

            if(this.custom_type == 'string' || this.custom_type == 'number'){
                custom.name = this.custom_name
                custom.value = this.custom_value
                custom.type = this.custom_type
            }
            else if(this.custom_type == 'list'){
                custom.name = this.custom_name
                custom.type = this.custom_type
                custom.params = {}
                custom.params.value = '0'
                custom.params.list = []

                for( let i = 0, len = this.model.length; i < len; i++){
                    custom.params.list.push({
                        name: this.model[i].text,
                        value: '' + i
                    })
                }
            }
            if(this.additionID){
                this.$emit('update:addition', this.addition.map(a => a.id == this.additionID
                                                    ? {...a, ...custom, id: this.additionID}
                                                    : a))
            }
            else{
                custom.id = '' + this.addition.length
                this.$emit('update:addition', [...this.addition, {...custom, id: 'f-' + this.addition.length}])
            }
            this.cancel()
        },
        editMode(){
            let val = this.addition.find(a => a.id == this.additionID)
            this.custom_name = val.name
            this.custom_type = val.type
            if(val.type == 'string' || val.type == 'number'){
                this.custom_value = val.value
            }
            else if(val.type == 'list'){
                for(let i =0, len = val.params.list.length; i < len; i++){
                    this.model.push({text: val.params.list[i].name})
                    this.items.push({text: val.params.list[i].name})
                }
            }
        },
        cancel(){
            this.custom_name = ''
            this.custom_value = ''
            this.custom_type = null
            this.model = []
            this.items.splice(1)
            this.$emit('update:component', 'edit')
        },
    },
    created(){
        if(this.additionID){
            this.editMode()
        }
    }
}
</script>
