<template>
    <div class="pt-3">
        <sl-vue-tree
            ref="tree"
            :allow-multiselect="true"
            v-model="folderslist"
            @select="nodeSelected"
            @toggle="expand"
            @drop="dropNode"
            @beforedrop="beforeDrop"
            @nodeclick="getDocs">

            <template slot="title" slot-scope="{ node }">
                <span
                    v-if="node.level > 1"
                    class="level-gap" v-for="i in node.level - 1"
                    >
                </span>
                <span
                    class="leaf-icon pl-1"
                    :class="['level-' + node.level]"
                    v-if="!node.data.group">
                    <v-icon size="18">icon-folder</v-icon>
                </span>
                <span
                    class="sl-vue-tree-title--name"
                    :class="[{'sl-group': node.data.group}, 'level-' + node.level]"
                    @click="expandGroup(node)"
                    >
                    {{ node.title }}
                </span>
            </template>

            <template
                slot="toggle"
                slot-scope="{ node }">
                <span
                    v-if="node.data.group || (node.level != 3 && node.children.length)"
                    :class="[{expanded: node.isExpanded}, 'level-' + node.level]">
                    <v-icon size="18">mdi-chevron-down</v-icon>
                </span>
                <span
                    v-if="node.level == 2 && !node.children.length">
                </span>
            </template>

            <template
                v-if="node.data.access"
                slot="sidebar"
                slot-scope="{ node }">
                <edit-menu
                    :node="node"
                    :editNode.sync="editNode"
                    :creatingBranchId.sync="creatingBranchId"
                    :isFile.sync="isFile"
                    @newFolderAction="newFolderAction"
                    @editFolderAction="editFolderAction"
                    @removeFolderAction="removeFolderAction"
                    @accessFolderAction="accessFolderAction"
                    />
            </template>

            <template
                slot="draginfo">
                <v-icon size="18">{{nodeSelectedEl.icon}}</v-icon>
                <span>

                    {{nodeSelectedEl.title}}
                </span>
            </template>

        </sl-vue-tree>

        <v-footer class="m-nav-footer pa-0 pr-2" fixed>
            <create-menu
                :node="0"
                :editNode.sync="editNode"
                :creatingBranchId.sync="creatingBranchId"
                :isFile.sync="isFile"
                :newGroup.sync="newGroup"
                :newFolder.sync="newFolder"/>
            <!--
            <edit-main
                :newFolder.sync="newFolder"
                :editFolder.sync="editFolder"
                :removeFolderFlag.sync="removeFolderFlag"
                />
            -->
        </v-footer>
        <create-group
            :newGroup.sync="newGroup"
            @createGroup="createGroup"
            />
        <create-folder
            :newFolder.sync="newFolder"
            @createFolder="createFolder"
            />
        <edit-folder
            :editFolder.sync="editFolder"
            :node="editNode"
            @renameNode="renameNode"
            />
        <remove-folder
            :removeFolderFlag.sync="removeFolderFlag"
            :node="editNode"
            @removeNode="removeNode"
            />
        <access-rights
            :accessrights.sync="accessrights"
            :item="editNode"
            />
    </div>
</template>

<script>
import SlVueTree from 'sl-vue-tree'
import { service, sortObj } from '@/utils/services'
import { v4 as uuidv4 } from 'uuid'

import { mapState } from "vuex"
import CreateMenu from './CreateMenu'
import CreateGroup from './CreateGroup'
import CreateFolder from './CreateFolder'
import EditMenu from './EditMenu'
import EditFolder from './EditFolder'
import RemoveFolder from './RemoveFolder'
import AccessRights from './AccessRights'
import EditMain from './EditMain'

    export default {
        name: 'SidebarFolders',
        components: {
            SlVueTree,
            CreateMenu,
            CreateGroup,
            CreateFolder,
            EditMenu,
            EditFolder,
            RemoveFolder,
            AccessRights,
            EditMain
        },
        data () {
            return {
                folderActive: null,

                search: '',
                editNode: 0,
                nodeSelectedEl: {
                    id: '',
                    title: '',
                    icon: ''
                },
                creatingBranchId: false,
                isFile: {
                    parent: 0,
                    file: false,
                    group: false,
                    lvl: 0
                },
                newGroup: false,
                newFolder: false,
                editFolder: null,
                removeFolderFlag: false,
                accessrights: false,
                drop: false,
                curentNodeId: '',
                stackTree: []
            }
        },
        computed: {
            folderslist: {
                get(){
                    let map = this.$store.state.workflow.foldersMap
                    let perms = (this.$store.state.administration.folderPermissions || [])
                    let groupPerms = this.$store.state.administration.groupPermissions
                    let curF = this.$store.state.workflow.currentFolder
                    let tree = service.createTreeForSidebar(map, curF)
                    return tree
                },
                set(v){
                }
            },
            getCurFol(){
                return this.$store.state.workflow.currentFolder
            }
        },
        methods: {
            getAccess(i, ar){
                let access = true
                ar.map(a => {
                    access = access && ((i >> a) & 1)
                })
                return !!access
            },
            expand(n){
                let list = this.getNodes()
                list = list.filter(i => i.isExpanded)
                list = list.map(i => i.data.id)

                this.$store.dispatch('workflow/setExpanded', list)
            },
            nodeSelected(n){
                let el = n[0]
                this.folderActive = el

                if(this.getAccess(el.data.access, [0])){
                    this.$store.commit('workflow/setCurrentFolder', el.data.id)
                }

                this.nodeSelectedEl.id = el.data.id
                this.nodeSelectedEl.title = el.title
                this.nodeSelectedEl.icon = el.isLeaf ? 'icon-folder' : 'mdi-chevron-down'
            },
            getDocs(i){
                //console.log(i)
                // return
                // this.curentNodeId = i.data.id
                // this.$store.commit('workflow/setActiveFolder', {
                //     id: i.data.id,
                //     name: i.title
                // })
            },
            beforeDrop(node, pos){
                this.stack = JSON.parse(JSON.stringify(this.$store.state.workflow.foldersMap))
                node = node[0]
                if(!((node.data.access >> 1) & 1)){
                    return
                }
                if((node.data.group && pos.placement == 'inside') ||
                    (node.data.group && pos.placement != 'inside' && pos.node.level > 1)){
                        this.drop = false
                }
                else{
                    this.drop = true
                }
            },
            dropNode(node, pos){
                if(this.drop){
                    let listPromises = []
                    let body = {
                        id: node[0].data.id,
                        name: node[0].title
                    }
                    let map = JSON.parse(JSON.stringify(this.$store.state.workflow.foldersMap))

                    if(pos.placement == 'inside'){
                        let list = map.filter(i => i.parent == pos.node.data.id && !!i.group == !!node[0].data.group)
                        body.order = list.length
                        body.parent = !!pos.node.data.group ? node[0].data.id : pos.node.data.id

                        listPromises.push(this.$store.dispatch('workflow/addFolder', body))

                        if(!!pos.node.data.group){
                            let group = map.filter(i => i.id === pos.node.data.id)[0]
                            group.folders = group.folders || []
                            group.folders.push({
                                folder: body.id,
                                order: body.order
                            })
                            listPromises.push(this.$store.dispatch('workflow/addGroup', {
                                name: group.name,
                                order: group.order,
                                folders: group.folders
                            }))
                        }

                        list = map.filter(i => i.parent == node[0].data.parent && i.id != node[0].data.id && !i.group)
                        list = sortObj(list, 'order')

                        list = list.map((i, j) => {
                            listPromises.push(this.$store.dispatch('workflow/addFolder', {
                                id: i.id,
                                name: i.name,
                                parent: !!i.toGroup ? i.id : i.parent ? i.parent : i.id,
                                order: j
                            }))
                            return {...i, order: j}
                        })
                        if(!!node[0].data.toGroup){
                            let group = map.filter(i => i.id === node[0].data.toGroup)[0]

                            listPromises.push(this.$store.dispatch('workflow/addGroup', {
                                name: group.name,
                                order: group.order,
                                folders: list.map(i => {
                                    return {
                                        folder: i.id,
                                        order: i.order
                                    }
                                })
                            }))
                        }
                    }
                    else{
                        if(!!node[0].data.group){
                            let listGroup = map.filter(i => !!i.group && i.id != node[0].data.id)
                            listGroup = sortObj(listGroup, 'order')
                            let group = map.filter(i => i.id == node[0].data.id)[0]
                            let indexIn = 0

                            if(listGroup.length > 0){
                                if(!pos.node.data.group){
                                    listGroup.push(group)
                                    listGroup.map((i, j) => {
                                        listPromises.push(this.$store.dispatch('workflow/addGroup', {
                                            name: i.name,
                                            order: j,
                                            folders: i.folders
                                        }))
                                    })
                                }
                                else{
                                    listGroup.map((i, j) => {
                                        if(i.id == pos.node.data.id){
                                            indexIn = j
                                        }
                                    })
                                    if(pos.placement == 'before'){
                                        listGroup.splice(indexIn, 0, group)
                                    }
                                    else{
                                        listGroup.splice(indexIn + 1, 0, group)
                                    }
                                    listGroup.map((i, j) => {
                                        listPromises.push(this.$store.dispatch('workflow/addGroup', {
                                            name: i.name,
                                            order: j,
                                            folders: i.folders
                                        }))
                                    })
                                }
                            }
                        }
                        else{
                            let folder = map.filter(i => i.id == node[0].data.id)[0]
                            let indexIn = 0

                            let listFoldersIn = map.filter(i => !i.group  &&
                                            i.parent == pos.node.data.parent   &&
                                            i.id != folder.id)
                            listFoldersIn = sortObj(listFoldersIn, 'order')
                            listFoldersIn.map((i, j) => {
                                if(i.id == pos.node.data.id){
                                    indexIn = j
                                }
                            })

                            if(pos.node.data.toGroup == 'root'){
                                listFoldersIn.splice(0, 0, folder)
                            }
                            else{
                                if(pos.placement == 'before'){
                                    listFoldersIn.splice(indexIn, 0, folder)
                                }
                                else{
                                    listFoldersIn.splice(indexIn + 1, 0, folder)
                                }
                            }

                            let listIn = listFoldersIn.map((i, j) => {
                                listPromises.push(this.$store.dispatch('workflow/addFolder', {
                                    id: i.id,
                                    name: i.name,
                                    parent: !!i.toGroup ? i.id : i.parent ? i.parent : i.id,
                                    order: j
                                }))
                                return {...i, order: j}
                            })
                            if(pos.node.data.toGroup && pos.node.data.toGroup != 'root'){
                                let group = map.filter(i => i.id == pos.node.data.toGroup)[0]

                                listPromises.push(this.$store.dispatch('workflow/addGroup', {
                                    name: group.name,
                                    order: group.order,
                                    folders: listIn.map(j => {
                                        return {
                                            folder: j.id,
                                            order: j.order
                                        }
                                    })
                                }))
                            }

                            if(node[0].data.parent != pos.node.data.parent){
                                let listOut = map.filter(i => !i.group &&
                                                i.parent == node[0].data.parent &&
                                                i.id != node[0].data.id)
                                listOut = sortObj(listOut, 'order')
                                listOut = listOut.map((i, j) => {
                                    listPromises.push(this.$store.dispatch('workflow/addFolder', {
                                        id: i.id,
                                        name: i.name,
                                        parent: !!i.toGroup ? i.id : i.parent ? i.parent : i.id,
                                        order: j
                                    }))
                                    return {...i, order: j}
                                })

                                if(node[0].data.toGroup){
                                    let group = map.filter(i => i.id == node[0].data.toGroup)[0]

                                    listPromises.push(this.$store.dispatch('workflow/addGroup', {
                                        name: group.name,
                                        order: group.order,
                                        folders: listOut.map(j => {
                                            return {
                                                folder: j.id,
                                                order: j.order
                                            }
                                        })
                                    }))
                                }
                            }
                        }
                    }

                    Promise.all(listPromises)
                    .then(() => {
                        return this.$store.dispatch('workflow/getFolders')
                    })
                    .then(() => {
                        return this.$store.dispatch('workflow/getGroups')
                    })
                    .then(res => {
                        this.$store.dispatch('workflow/createFoldersMap')
                    })
                }
                else{
                    this.$store.commit('workflow/setFoldersMap', [])
                    this.$store.commit('workflow/setFoldersMap', this.stack)
                }
            },
            expandGroup(node){
                return
                const slVueTree = this.$refs.tree
                !node.isExpanded ? this.setGroup(node.data.id) : this.setGroup(null)
                !node.isExpanded && slVueTree.updateNode(node.path, {isExpanded: !node.isExpanded})
            },
        renameNode(v){
            let currentF = JSON.parse(JSON.stringify(this.folderActive))

            let currentFolderId = ''
            let listPromises = []
            let body = {}
            if(!currentF.data.group){
                body = {
                    id: currentF.data.id,
                    name: v,
                    parent: currentF.data.parent == currentF.data.toGroup ? currentF.data.id : currentF.data.parent,
                    order: currentF.data.order
                }
                currentFolderId = currentF.data.id
                listPromises.push(this.$store.dispatch('workflow/addFolder', body))
            }
            else{
                let group = this.$store.state.workflow.foldersMap.filter(i => i.id == currentF.data.id)[0]
                body = {
                    name: group.name,
                    order: currentF.data.order,
                    created_at: group.created_at,
                    created_by: group.created_by,
                    updated_at: group.updated_at,
                    updated_by: group.updated_by,
                    folders: currentF.data.list || null
                }
                listPromises.push(this.$store.dispatch('workflow/removeGroup', body))
                body = {
                    name: v,
                    order: currentF.data.order,
                    folders: currentF.data.list || null
                }
                currentFolderId = 'group_' + v
                listPromises.push(this.$store.dispatch('workflow/addGroup', body))
            }

            Promise.all(listPromises)
            .then(() => {
                return this.$store.dispatch('workflow/getFolders')
            })
            .then(() => {
                return this.$store.dispatch('workflow/getGroups')
            })
            .then(res => {
                return new Promise((resolve) => {
                    resolve(this.$store.dispatch('workflow/createFoldersMap'))
                })
            })
            .then(() => {
                this.$store.commit('workflow/setCurrentFolder', currentFolderId)
            })
        },
        removeFolderAction(v){
            this.removeFolderFlag = v
        },
        editFolderAction(v){
            this.editFolder = v
        },
        newFolderAction(v){
            this.newFolder = v
        },
        accessFolderAction(v){
            Promise.resolve(
                this.editNode = {
                    ...this.editNode,
                    id: this.editNode.data.id,
                    type: 'folder',
                    name: this.editNode.title
                }
            )
            .then(() => {
                this.accessrights = true
            })
        },
        removeNode(){
            let currentF = JSON.parse(JSON.stringify(this.folderActive))
            let listPromises = []
            listPromises.push(this.$store.commit('workflow/setCurrentFolder', currentF.data.parent))

            if(!!currentF.data.group){
                let group = this.$store.state.workflow.foldersMap.filter(i => i.id == currentF.data.id)[0]
                let list = group.folders || []
                list.map((f, i) => {
                    listPromises.push(this.$store.dispatch('workflow/removeFolder', {uuid: f.folder}))
                })
                let g_model = {
                    name: group.name,
                    order: group.order,
                    created_at: group.created_at,
                    created_by: group.created_by,
                    updated_at: group.updated_at,
                    updated_by: group.updated_by,
                    folders: group.folders
                }
                group = g_model

                listPromises.push(this.$store.dispatch('workflow/removeGroup', group))
            }
            else{
                listPromises.push(this.$store.dispatch('workflow/removeFolder', {uuid: currentF.data.id}))
            }

            Promise.all(listPromises)
            .then(() => {
                return this.$store.dispatch('workflow/getFolders')
            })
            .then(() => {
                return this.$store.dispatch('workflow/getGroups')
            })
            .then(res => {
                return this.$store.dispatch('workflow/createFoldersMap')
            })
            .then(() => {
                let parent = currentF.data.parent
                this.$store.commit('workflow/setCurrentFolder', 0)
                this.$store.commit('workflow/setCurrentFolder', parent)
            })
        },
        createGroup(v){
            let list = this.$store.state.workflow.foldersMap.filter(i => !!i.group)
            let group = {
                name: this.checkTween(list, v.name),
                order: list.length
            }

            this.$store.dispatch('workflow/addGroup', group)
            .then(() => {
                return this.$store.dispatch('workflow/getGroups')
            })
            .then(res => {
                this.$store.dispatch('workflow/createFoldersMap')
            })
        },

        createFolder(v){
            let currentF = JSON.parse(JSON.stringify(this.editNode)) || 0
            let parent = currentF ? currentF.data.id : 0
            let list = this.$store.state.workflow.foldersMap.filter(i => i.parent == parent && !i.group)

            let id = uuidv4()
            let folder = {
                id: id,
                parent: currentF ? currentF.data.group ? id : currentF.data.id : id,
                name: this.checkTween(list, v.name),
                order: list.length
            }

            let group = false
            if(currentF && currentF.data.group){
                group = this.$store.state.workflow.foldersMap.filter(i => i.id == currentF.data.id)[0]

                let g_model = {
                    name: group.name,
                    order: group.order,
                    created_at: group.created_at,
                    created_by: group.created_by,
                    updated_at: group.updated_at,
                    updated_by: group.updated_by,
                    folders: group.folders || []
                }
                group = g_model
            }

            this.$store.dispatch('workflow/addFolder', folder)
            .then(res => {
                let listPromises = []
                if(group){
                    group.folders.push({folder: res.id, order: 0})
                    listPromises.push(this.$store.dispatch('workflow/addGroup', group))
                }
                let perms = this.$store.state.administration.folderPermissions
                                .find(f => f.id == parent)
                if(!perms){
                    let getUser = this.$store.state.administration.user
                    perms = {
                        users: [{
                            id: getUser.id + '',
                            email: getUser.email,
                            full_name: getUser.first_name + ' ' + getUser.last_name,
                            can_open: true,
                            can_edit: true,
                            can_download: true,
                            can_comment: true
                        }]
                    }
                }
                listPromises.push(this.$store.dispatch('administration/setFolderPermissions', {
                    id: res.id,
                    body: {
                        id: res.id,
                        users: perms.users,
                        groups: this.$store.state.administration.groupPermissions.map(p => {
                            return {
                                id: p.id,
                                name: p.name,
                                can_open: true,
                                can_edit: true,
                                can_download: true,
                                can_comment: true
                            }
                        }),
                    }
                }))
                return Promise.all(listPromises)
            })
            .then(() => {
                return this.$store.dispatch('workflow/getFolders')
            })
            .then(() => {
                return this.$store.dispatch('workflow/getGroups')
            })
            .then(res => {
                this.$store.dispatch('workflow/createFoldersMap')
            })
            .then(() => {
                return this.$store.dispatch('administration/getFoldersPermissions')
            })
        },
        checkTween(ar, v){
            let name = v
            ar.map(i => {
                if(i.name == name){
                    name += '0'
                }
            })
            return name
        },
            getGroupsFolders(v){
                let groups = JSON.parse(JSON.stringify(this.$store.state.workflow.groups))
                let folders = JSON.parse(JSON.stringify(this.$store.state.workflow.folders))

                Promise.all([
                    this.$store.dispatch('workflow/updateFolders', {
                        paths: folders,
                        parent: v.currentFolder
                    }),
                    this.$store.dispatch('workflow/updateGroups', groups)
                ])
                .finally(() => {
                    this.$store.dispatch('workflow/createTree')
                })
            },
            traverseTree(ar, list = [], map = [], parent = ''){
                ar.map(i => {
                    let path = parent + '/' + i.title
                    list.push(path)
                    map.push({
                        id: i.data.id,
                        prevPath: i.data.path,
                        nextPath: path
                    })
                    if(i.children.length){
                        this.traverseTree(i.children, list, map, path)
                    }
                })
                return {list, map}
            },
            getGroupsFoldersFromTree(){
                let currentF = JSON.parse(JSON.stringify(this.folderActive))
                let tree = this.$store.state.workflow.folderTree
                let groups = tree.filter(i => i.data.group)
                let folders = []
                let map = []

                let groupList = groups.map(i => {
                    folders = [...folders, ...i.children]
                    let traverse = this.traverseTree(i.children)
                    let group = {
                        group: i.title,
                        folders: traverse.list
                    }
                    map.push({
                        id: i.data.id,
                        prevPath: i.data.path,
                        nextPath: i.data.path
                    })
                    map = [...map, ...traverse.map]
                    return group
                })
                let dontGroupList = tree.filter(i => !i.data.group)
                folders = [...folders, ...dontGroupList]
                let traverse = this.traverseTree(folders)
                let folderList = traverse.list
                map = [...map, ...traverse.map]

                let selectF = '/'
                map.map(i => {
                    if(i.id == currentF.data.id){
                        selectF = i.nextPath
                    }
                })

                Promise.all([
                    this.$store.dispatch('workflow/updateFolders', {
                        paths: folderList,
                        //parent: selectF
                    }),
                    this.$store.dispatch('workflow/updateGroups', groupList)
                ])
                .finally(() => {
                    this.$store.dispatch('workflow/createTree')
                })
            },
            getNodes(){
                let list = []
                const slVueTree = this.$refs.tree
                slVueTree.traverse(function(o){
                    list.push(Object.assign({}, o))
                })
                return list
            },
        },
        mounted(){
            this.unwatch = this.$store.watch((state) => state.workflow.openFolder, n => {
                let o = {
                    data: n,
                    title: n.name
                }
                let nodePath = []
                this.getNodes().map(i => {
                    if(i.data.path == n.path){
                        nodePath = i.path
                    }
                })
                const slVueTree = this.$refs.tree
                slVueTree.select(nodePath)
                //this.getDocs(o)
            })
        },
        beforeDestroy(){
            this.unwatch()
        }
    }
</script>
