<template>
<v-container fluid fill-height text-xs-center class="m-container pb-10 pt-0">
<v-layout row wrap>
<v-flex xs12>
    <v-container fluid px-3 pb-10 pt-0 class="m-container" ref="wrap">

        <v-toolbar flat height="auto" class="m-table-toolbar">
            <v-text-field
                v-model="search"
                prepend-icon="search"
                placeholder="..."
                single-line
                hide-details
                class="m-table-toolbar--search ml-2"
                ></v-text-field>

            <v-spacer></v-spacer>
            <!--
            <v-btn
                text
                @click.stop="createXml = true"
                class="ma-0 m-btn--text_icon">
                <v-icon
                    size="16"
                    left>mdi-plus-circle-outline</v-icon> XML 
            </v-btn>
        -->
            <v-btn
                text
