                class="ma-0 m-btn--text_icon">
                <v-icon
                    size="16"
                    left>mdi-plus-circle-outline</v-icon> 
            </v-btn>
            <v-btn
                :disabled="!checkFolder"
                outlined
                color="primary"
                @click.stop="upload"
                class="ma-0 m-btn--text_icon">
                <v-icon
                    size="18"
                    left>icon-upload</v-icon>
            </v-btn>
        <!--
            <div class="m-spacer--v"></div>
            <v-btn
                v-for="(i, j) in table_type_list"
                :key="j"
                depressed
                :color="workflow_project == i.name ? '#e1e1e1' : '#fafafa'"
                class="m-btn-icon--cube"
                :class="{active: workflow_project == i.name}"
                @click="setTableType(i.name)">
                <v-icon
                    color="#6e6e6e"
                    size="25">{{i.icon}}</v-icon>
            </v-btn>
        -->
        </v-toolbar>

        <v-toolbar flat height="auto" class="m-table-toolbar pa-3 m-table-toolbar--path">
            <span
                v-for="(folder, i) in getPath"
                :key="folder.id + i + ''"
            >
                <span
                    v-if="folder.link"
                    class="m-table-toolbar--path_link"
                    @click="openFolder(folder)"
                >{{folder.name}}</span>
                <span v-if="folder.link">/</span>
                <span
                    v-if="!folder.link"
                >{{folder.name}}</span>
