            disable-pagination
            hide-default-footer
            class="m-table-custom"
        >
            <template v-slot:no-data>
                {{noData}}
            </template>

            <template v-slot:header.collisions="{item}">
                <v-icon
                    size="20"
                    color="#767676">table-collision
                </v-icon>
            </template>

            <template v-slot:header.tasks="{item}">
                <v-icon
                    size="20"
                    color="#767676">trd-ticket
                </v-icon>
            </template>

            <template v-slot:item.tasks="{item}">
                <v-icon
                    v-if="item.task"
                    size="20"
                    color="#767676">trd-ticket
                </v-icon>
            </template>

            <template v-slot:item.sp333="{item}">
                <v-tooltip v-if="item.inwork !== true && (item.sp333.type == 'error'
                    && item.sp333.value == 1
                    || item.sp333.type == 'classerror'
                    || item.sp333.type == 'class.error'
                    || item.sp333.type == 'attribute.error'
                    || item.sp333.type == 'classerror.onlyempty')" bottom right color="#fff" max-width="250px" content-class="m-tooltip--info">
                    <template v-slot:activator="{ on }">
                        <v-icon
                            v-on="on"
                            style="cursor: pointer;"
                            color="#fc0000"
                            @click="setSP333Attr(item)"
                            size="20">mdi-alert-outline</v-icon>
                    </template>
                    <template v-slot:default>
                        <span class=""><b> .</b><br>
      .
                        </span>
                    </template>
                </v-tooltip>
                <v-tooltip v-if="item.sp333.type == 'warning'
                    && item.inwork !== true" bottom right color="#fff" max-width="250px" content-class="m-tooltip--info">
                    <template v-slot:activator="{ on }">
                        <v-icon
                            v-on="on"
                            style="cursor: pointer;"
                            color="#ff904b"
                            @click="setSP333Attr(item)"
                            size="20">mdi-alert-outline</v-icon>
                    </template>
                    <template v-slot:default>
                        <span class="">      .
                        </span>
                    </template>
                </v-tooltip>
            </template>

            <template v-slot:item.collisions="{item}">
            <!--
                <v-tooltip
                    v-if="item.collisions == 1
                        && item.inwork !== true"
                    bottom right color="#fff" max-width="250px" content-class="m-tooltip--info">
                    <template v-slot:activator="{ on }">
                        <v-icon
                            v-on="on"
                            style="cursor: pointer;"
                            color="#000000"
                            @click="setCollisions(item)"
                            size="20">table-collision_black</v-icon>
                    </template>
                    <template v-slot:default>
                        <span class="">      </span>
                    </template>
                </v-tooltip>
            -->
            </template>

            <template v-slot:item.data-table-select="{ item, isSelected, select }">
                <v-simple-checkbox
                    v-if="item.inwork !== true && getAccess(item.access, 'or', [1, 2])"
                    :value="isSelected && item.inwork !== true && getAccess(item.access, 'or', [1, 2])"
                    :disabled="item.inwork === true && getAccess(item.access,'or', [1, 2])"
                    @input="select($event)">
                </v-simple-checkbox>
            </template>

            <template v-slot:item.name="{ item, index }">
                <div class="m-table--name">
                    <span
                        v-if="item.type == 'folder' && getAccess(item.access, 'and', [0])"
                        class="m-text--hover"
                        :class="{'m-item--sub': item.sub}"
