                        >{{item.name}}
                    </span>
                    <span
                        v-else-if="item.type != 'folder'
                            && !item.inwork
                            && getAccess(item.access, 'and', [0])"
                        :class="{'m-text--hover': item.status_file !== 3 && checkLicense('editRVT')}"
                        @click="item.status_file !== 3 && checkLicense('editRVT') ? getDct(item) : ';'"
                        >
                        <v-tooltip top :transition="'100'">
                            <template v-slot:activator="{ on, attrs }">
                                <span
                                    :class="{'m-item--sub': item.sub}"
                                    role="empty"
                                    v-bind="attrs"
                                    v-on="on">
                                    {{item.name_short}}
                                </span>
                            </template>
                            {{item.name}}
                        </v-tooltip>
                    </span>
                    <span
                        v-else-if="item.type != 'folder' && item.inwork"
                        :class="{'m-item--sub': item.sub}"
                        >{{item.name_short}}
                    </span>
                    <span
                        v-else
                        :class="{'m-item--sub': item.sub}"
                        >{{item.name_short || item.name}}
                    </span>
                    <v-tooltip
                        v-if="item.status_file === 3"
                        top
                        right
                        color="#fff"
                        max-width="250px"
                        content-class="m-tooltip--info">
                        <template v-slot:activator="{ on }">
                            <v-icon
                                size="20"
                                v-on="on"
                                class="mr-1 m-info--file"
                                color="#ff0000">mdi-alert-outline
                            </v-icon>
                        </template>
                        <template v-slot:default>
                            <span class="">    .       .</span>
                        </template>
                    </v-tooltip>
                </div>
            </template>

            <template v-slot:item.sub="{ item }">
                <span
                    @click="expandSub(item)">
                    <v-icon
                        v-show="!!item.group || !!item.consolidations"
                        :style="{margin: '0 -4px', cursor: 'pointer'}"
                        :class="{'m-item--expanded__icon': item.expanded}"
                        size="20">mdi-chevron-down</v-icon>
                </span>
            </template>

            <template v-slot:item.src="{ item }">
                <v-icon
                    v-if="!!item.group"
                    size="30">icon-group</v-icon>
                <v-img
                    v-else-if="item.type != 'folder' && item.inwork === true"
                    width="35"
                    class="mx-auto"
                    :class="{'m-item--sub': item.sub}"
                    :src="inwork"></v-img>
                <v-icon
                    v-else-if="item.type == 'folder'"
                    :class="{'m-item--sub': item.sub}"
                    size="30">icon-folder</v-icon>
                <v-icon
                    v-else
                    :class="{'m-item--sub': item.sub}"
                    size="30">ft-{{item.type == 'type' ? 'unknown' : item.type}}</v-icon>
                </v-icon>
            </template>

            <template v-slot:item.action="{ item }">
                <action-menu
                    v-if="item.inwork !== true && !checkSelected(item) && item.access"
                    @download="downloadFile"
                    @convertationIFCXML="convertationIFCXML"
                    @getDct="getDct"
                    @removeItem="removeItem"
                    @openFolder="openFolder"
                    @replaceItems="replaceItems"
                    @copyItems="copyItems"
                    @renameitem="renameitem"
                    @accessRights="accessRights"
                    @versionJournal="versionJournal"
                    @errorIFCXML="errorIFCXML"
                    @onSpecificationXml="onSpecificationXml"
                    :access="item.access"
                    :item="item"
                />
            </template>
        <!--
            <template v-slot:item.status="{ item }">
                <span  v-if="item.type != 'folder'">
                    {{status[item.status]}}
                </span>
            </template>
        -->
        </v-data-table>

        <table-grid
            v-if="workflow_project == 'grid'"
            :list="getDocs"
            :headers="headers"
            :actions="[getDct]"
        >

            <template v-slot:name="{item}">
                <div>
                {{item.name}}
                </div>
            </template>
        </table-grid>

        <upload-files
            :uploadF.sync="uploadF"
            :project_id="id"
        />

        <v-card
            class="m-table-custom--action"
            :style="cardActionStyle"
            v-show="selected.length && getAccessSelected('or', [0, 1, 2])"
            >
            <v-card-actions>
                <v-btn
                    v-show="getAccessSelected('and', [2])"
                    text
                    color="primary"
                    class="m-btn"
                    @click="download">
                    <v-icon size="20" class="mr-1" color="#0070e0">mdi-download</v-icon></v-btn>
                <v-btn
                    v-show="getAccessSelected('and', [1])"
                    text
                    color="primary"
                    class="m-btn"
                    @click="removeSelectedFlag = true">
                    <v-icon size="20" class="mr-1" color="#0070e0">mdi-trash-can-outline</v-icon>
                    
                </v-btn>
                <v-btn
                    v-show="getAccessSelected('and', [1])"
                    text
                    color="primary"
                    class="m-btn"
                    @click="copyItem = true">
                    <v-icon size="20" class="mr-1" color="#0070e0">mdi-content-copy</v-icon>
                    
                </v-btn>
                <v-btn
                    v-show="getAccessSelected('and', [1])"
                    text
                    color="primary"
                    class="m-btn"
                    @click="replaceItem = true">
                    <v-icon size="20" class="mr-1" color="#0070e0">mdi-file-replace-outline</v-icon>
                    
                </v-btn>
                <v-btn
                    v-show="getAccessSelected('and', [0]) && checkTypeFiles({
                        type: ['rvt', 'ifc'],
                        count: 'lot'
                        })"
                    text
                    color="primary"
                    class="m-btn"
                    @click="setConsolidations">
                    <v-icon size="20" class="mr-1" color="#0070e0">trd-conect</v-icon>
                    
                </v-btn>
            <!--
                <v-btn
                    v-show="getAccessSelected('and', [1]) && checkTypeFiles({
                        type: ['rvt'],
                        count: 'one'
                        })"
                    text
                    color="primary"
                    class="m-btn"
                    @click="delta">
                    <v-icon size="20" class="mr-1" color="#0070e0">trd-version1</v-icon>
                    
                </v-btn>
            -->
            </v-card-actions>
        </v-card>

        <remove-selected
            :removeSelectedFlag.sync="removeSelectedFlag"
            @removeSelected="removeSelected"
            />

        <replace-item
            :replaceItem.sync="replaceItem"
            :selected="getReplaceItems"
            :items="getDocs"
            @clearSelected="clearSelected"
            />
        <copy-item
            :copyItem.sync="copyItem"
            :selected="getReplaceItems"
            :items="getDocs"
            @clearSelected="clearSelected"
            />
        <rename-item
            :renameItem.sync="renameItem"
            :item="workItem"
            />
        <remove-item
            @removeFolder="removeFolder"
            @removeDct="removeDct"
            :remove_item.sync="remove_item"
            :item="workItem"
            />
        <create-folder
            :newFolder.sync="newFolder"
            @createFolder="createFolder"
            />
        <create-xml
            :createXml.sync="createXml"
            @createFolder="createFolder"
            />
        <access-rights
            :accessrights.sync="accessrights"
            :item="workItem"
            />
        <version-journal
            :versionjournal.sync="versionjournal"
            :item="workItem"
            />
        <error-ifcxml
            :error_ifcxml.sync="error_ifcxml"
            />
        <set-sp333-attribute
            :SP333attr.sync="SP333attr"
            :SP333JSON="SP333JSON"
            :item="workItem"
            />
        <collisions
            :collisions.sync="collisions"
            :collisionsJSON.sync="collisionsJSON"
            />
        <specification-xml
            :specification_xml.sync="specification_xml"
