        downloadFile(f){
            let url = ''
            let name = ''
            if(f.type !== 'ifcxml'){
                url = f.file.files[0].url
                name = f.file.name
            }
            else{
                url = f.file.files[0].url + '/ifcxml.zip'
                name = f.file.name_short + '.ifcxml.zip'
            }

            let uuid = uuidv4()
            this.$store.dispatch('common/setUploadNote', [{
