                uuid: uuid,
                action: 'download',
                pushin: true
            }])
            this.$store.dispatch('common/getFile', {
                id: url,
                uuid: uuid,
                typeBlob: 'blob'
            })
            .then(res => {
                this.$store.dispatch('common/setUploadNote', [{
                    uuid: uuid,
                    action: 'download',
                    pushin: false
                }])
                if(!res.error){
                    let a = document.createElement("a")
                    let objectURL = URL.createObjectURL(res.file)
                    a.href = objectURL
                    a.download = name
                    document.body.appendChild(a)
                    a.click()
                    URL.revokeObjectURL(objectURL)
                    a.remove()
                }
                else{
                    console.log('downloadFile error', err)
                }
            })
            .catch(err => {
                console.log('downloadFile error', err)
            })
        },
        download(){
            let list = this.selected
            let listPromises = []
            let listFilename = []
            list.map(f => {
                if(f.type !== 'folder'){
                    let file = f.files[0]
                    let uuid = uuidv4()
                    this.$store.dispatch('common/setUploadNote', [{
                        name: f.name,
                        uuid: uuid,
                        action: 'download',
                        pushin: true
                    }])
                    listFilename.push({
                        id: file.url,
                        uuid: uuid,
                        name: f.name
                    })
                    listPromises.push(this.$store.dispatch('common/getFile', {
                        id: file.url,
                        name: f.name,
                        uuid: uuid
                    }))
                }
            })

            if(!listFilename.length){
                return
            }
            Promise.all(listPromises)
            .then(res => {
                let zip = new JSZip()
                let resultat = res
                    .filter(f => !f.error)
                    .map((r, j) => {
                        let file = listFilename.find(n => n.id == r.id)
                        this.$store.dispatch('common/setUploadNote', [{
                            uuid: r.uuid,
                            action: 'download',
                            pushin: false
                        }])
                        zip.file(file.name, r.file)
                        URL.revokeObjectURL(r.file)
                        return j
                    })
                return resultat.length ? zip.generateAsync({type: 'base64'}) : null
            })
            .then(res => {
                if(res){
                    let a = document.createElement("a")
                    a.href = "data:application/zip;base64," + res
                    a.download = 'unitbimprojectfiles.zip'
                    document.body.appendChild(a)
                    a.click()
                    a.remove()
                }
            })
        },
        searchFolder(tree, folder, list = []){
            for(let i = 0, len = tree.length; i < len ; i++){
                if(tree[i].data.path == folder){
                    list = tree[i].children
                    return list
                }
                else{
                    list = this.searchFolder(tree[i].children, folder, list)
                    if(list.length){
                        return list
                    }
                }
            }
            return list
        },
        createTree(ar){
            let result = []
            let level = {result}

            ar.map(path => {
                let a = path.split('/').slice(1)

                a.reduce((r, title, i, a) => {
                    if(!r[title]){
                        r[title] = {result: []}
                        r.result.push({
                            title,
                            data: {path: path},
                            isExpanded: false,
                            children: r[title].result
                        })
                    }

                    return r[title]
                }, level)
            })
            result = [{
                title: '',
                data: {path: '/'},
                children: result
            }]
            return result
        },
        createFolder(v){
            let state = this.$store.state.workflow
            let currentF = state.currentFolder
            if(currentF){
                currentF = state.foldersMap.filter(i => i.id == currentF)[0]
            }

            let parent = currentF ? currentF.id : 0
            let list = state.foldersMap.filter(i => i.parent == parent && !i.group)

            let id = uuidv4()
            let folder = {
                id: id,
                parent: currentF
                    ? currentF.group
                        ? id
                        : currentF.id
                    : id,
                name: this.checkTween(list, v.name),
                order: list.length
            }

            let group = false
            if(currentF && currentF.group){
                group = state.foldersMap.filter(i => i.id == currentF.id)[0]

                let g_model = {
                    name: group.name,
                    order: group.order,
                    created_at: group.created_at,
                    created_by: group.created_by,
                    updated_at: group.updated_at,
                    updated_by: group.updated_by,
                    folders: group.folders || []
                }
                group = g_model
            }

            this.$store.dispatch('workflow/addFolder', folder)
            .then(res => {
                this.$notify({
                    group: 'note',
                    type: res.notify ? 'success' : 'error',
                    text: res.notify
                        ? '  ' + res.name
                        : ': ' + res.data.error
                })
                if(res.notify){
                    let listPromises = []
                    if(group){
                        group.folders.push({folder: res.id, order: 0})
                        listPromises.push(this.$store.dispatch('workflow/addGroup', group))
                    }
                    let perms = this.$store.state.administration.folderPermissions
                    if(!perms || !perms.find(f => f.id == currentF.id)){
                        let getUser = this.$store.state.administration.user
                        perms = {
                            users: [{
                                id: getUser.id + '',
                                email: getUser.email,
                                full_name: getUser.first_name + ' ' + getUser.last_name,
                                can_open: true,
                                can_edit: true,
                                can_download: true,
                                can_comment: true
                            }]
                        }
                    }
                    else{
                        perms = perms.find(f => f.id == currentF.id)
                    }

                    listPromises.push(this.$store.dispatch('administration/setFolderPermissions', {
                        id: res.id,
                        body: {
                            id: res.id,
                            users: perms.users,
                            groups: this.$store.state.administration.groupPermissions.map(p => {
                                return {
                                    id: p.id,
                                    name: p.name,
                                    can_open: true,
                                    can_edit: true,
                                    can_download: true,
                                    can_comment: true
                                }
                            }),
                        }
                    }))
                    return Promise.all(listPromises)
                }
                else{
                    return Promise.reject(': ' + res.data.error)
                }
            })
            .then(() => {
                return this.$store.dispatch('workflow/getFolders')
            })
            .then(() => {
                return this.$store.dispatch('workflow/getGroups')
            })
            .then(res => {
                return this.$store.dispatch('workflow/createFoldersMap')
            })
            .then(() => {
                return this.$store.dispatch('administration/getFoldersPermissions')
            })
            .catch(er => {
                console.log(er)
            })
        },
        checkTween(ar, v){
            let name = v
            ar.map(i => {
                if(i.name == name){
                    name += '0'
                }
            })
            return name
        },
    },
    created(){
        let workflow = this.$store.state.workflow
        let project = workflow.projects.filter(i => i.id == this.$route.params.id)
        project = JSON.parse(JSON.stringify(project[0])) || null

        let projectName = project ? project.info.name : ''

        let title = [
            {title: '', path: '/workflow'},
            {title: projectName, path: ''}
        ]
        let clearPath = []
        if(!workflow.activeProject || this.id != workflow.activeProject.id){
            clearPath = [this.$store.commit('workflow/setExpanded', []),
            this.$store.commit('workflow/setCurrentFolder', 0)]
        }
        this.$store.commit('toolbar/setTitle', title)
        Promise.all([
            this.$store.commit('workflow/setActiveProject', project),
            ...clearPath,
            this.$store.dispatch('workflow/getFolders'),
            this.$store.dispatch('workflow/getGroups'),
            this.$store.dispatch('administration/getFoldersPermissions'),
            this.$store.dispatch('administration/getFilesPermissions'),
            this.$store.dispatch('workflow/getTaskProject', {
                type_id: 'c',
                id: this.$store.state.administration.company.id,
                project_uuid: project.id,
                filter: {}
            })
        ])
        .finally(res => {
            this.$store.dispatch('workflow/createFoldersMap')
        })
    },
