<template>
    <v-footer
        v-if="visible"
        id="core-footer"
        absolute
        class="pb-2"
    >
        <span>
            &copy;
            UnitBIM 2018.   .
        </span>
        <v-spacer />
        <span
            v-for="(i, j) in links"
            :key="i.name">
            <a
                class="m-footer-link"
                :href="i.link"
            >{{i.value}}</a>
            {{j != links.length - 1 ? '&nbsp;/&nbsp;' : ''}}
        </span>
    </v-footer>
</template>

<script>
    export default {
        data: () => ({
            visible: true,
            links: [{
                name: 'term',
                value: '   ',
                link: 'https://unitbim.ru/account/#/modules/term'
            },{
                name: 'privacy',
                value: '   ',
                link: 'https://unitbim.ru/account/#/modules/privacy'
            },{
                name: 'cookies',
                value: ' cookie',
                link: 'https://unitbim.ru/account/#/modules/cookies'
            }]
        }),
        watch: {
            '$route' (to, from) {
                this.display()
            }
        },
        methods:{
            display: function(){
                if(this.$route.name == 'workflow-view'){
                    this.visible = false
                }
                else{
                    this.visible = true
                }
            }
        },
        created(){
            this.display()
        }
    }
</script>
