<template>

    <v-card
        class="m-card-scrollable"
        :class="{'m-panel__hidden': minimize}"
        :style="styleP"
        >
        <v-card-title class="pr-0 pl-4 py-1 m-modal--title-size14 m-card-scrollable__title"> 
            <v-spacer></v-spacer>
            <v-btn
                text
                icon
                @click="minimize = !minimize"
                color="#7f7f7f">
                <v-icon size="20">
                    {{!minimize ? 'mdi-window-minimize' : 'mdi-window-maximize'}}
                </v-icon>
            </v-btn>
            <v-btn
                text
                icon
                @click="cancel"
                color="#7f7f7f">
                <v-icon size="20">close</v-icon>
            </v-btn>
        </v-card-title>
        <v-divider></v-divider>

        <div class="search">
            <!--<input type="text" class="search-field" v-model="message" values='message' placeholder="  c">
            <v-btn outlined color="normal" class="bottom_search" @click="search()"><img src="../../../../assets/fonts/search_icon.svg" alt="" class="search-icon"></v-btn>-->


            <v-text-field
                v-model="message"
                :append-icon="'search'"
                clear-icon="mdi-close-circle"
                label=""
                type="text"
                hide-details
                :placeholder="'  c'"
                @click:append="search"
            ></v-text-field>
        </div>
        <div class="treeviewCheckBox">
            <v-treeview  selectable :items="listTreeView" @input="onSelected" class="includeTreeView"></v-treeview>
        </div>
        <v-card-actions class="bottoms">
                <v-btn outlined :color="one ? 'primary' : 'normal'" class="m-btn m-btn--float" :class="{'m-btn-normal': !one}" @click="typeSearch = 'ID'; search(); setFilter('one');">ID</v-btn>
                <v-btn outlined :color="two ? 'primary' : 'normal'" class="m-btn m-btn--float" :class="{'m-btn-normal': !two}" @click="typeSearch = 'Name'; search(); setFilter('two');"></v-btn>
                <v-btn outlined :color="three ? 'primary' : 'normal'" class="m-btn m-btn--float" :class="{'m-btn-normal': !three}" @click="typeSearch = 'Parameters'; search(); setFilter('three');"></v-btn>
                <v-btn outlined :color="four ? 'primary' : 'normal'" class="m-btn m-btn--float" :class="{'m-btn-normal': !four}" @click="typeSearch = 'Category'; search(); setFilter('four');"> </v-btn>
        </v-card-actions>
        <v-card-title class="pa-0">
            <div class="bottom_id">
                <v-btn
                    class="m-btn"
                    text
                    tile
                    dense
                    :disabled="!list.length || !activeClsn"
                    :color="activeBox ? 'primary' : 'default'"
                    @click="turnActiveBox">
                    <v-icon
                        left
                        size="20">trd-section</v-icon><span class="m-text--size12"></span>
                </v-btn>

                <icon-botton
                    class="mr-4"
                    :disabled="!list.length"
                    :color="activeClsn && list.length? 'primary' : 'default'"
                    @onClick="turnActiveClsn"
                    >trd-cube_hide
                </icon-botton>

                <icon-botton
                    class="mr-4"
                    :disabled="!list.length"
                    :color="activeOnly && list.length? 'primary' : 'default'"
                    @onClick="turnActiveOnly"
                    >trd-cube_show
                </icon-botton>

            </div>

        </v-card-title>
        <v-card-text
            class="pa-0 pb-5 m-card-scrollable__text">

    <v-list dense>
      <v-subheader class="panelResults"></v-subheader>
      <v-list-item-group
        v-model="selectedItem"
        color="primary"
      >
        <v-list-item
          v-for="(item, i) in ParamSearch"
          :key="i"
        >

          <v-list-item-content class="resultsForPanel">
            <v-list-item-title v-if="typeSearch == 'Name'" v-text="item.Name"></v-list-item-title>
            <v-list-item-title v-if="typeSearch == 'ID'" v-text="item.id"></v-list-item-title>
            <v-list-item-title v-if="typeSearch == 'Parameters'" v-text="item.ParametersAll"></v-list-item-title>
            <v-list-item-title v-if="typeSearch == 'Category'" v-text="item.Category"></v-list-item-title>

          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>

        </v-card-text>

    </v-card>

</template>

<script>
import IconBotton from '@/components/custom/IconBotton'
import Components from './panel-tabs/Components'

export default {
    name: 'PanelSearch',
    components: {
        IconBotton
    },
    props: ['menuItem', 'list', 'postMessage','worker','ParamSearch','styleP'],
    data(){
        return {
            mode: [{
                name: 'clsn',
                value: false
            },{
                name: 'box',
                value: false
            },{
                name: 'only',
                value: false
            }],
            items: [],
            minimize: false,
            message: '',
            typeSearch: 'Name',
            searchWord: '',
            one: false,
            two: true,
            three: false,
            four: false,
            choiceRegistr: false,
            choiceWholly: false,
            listTreeView: [
                {
                    name: '',
                    children: [
                        { id: 1, name: ' '},
                        { id: 2, name: ' '},
                    ]
                }
            ],
            selectedItem: 0
        }
    },
    computed: {
        activeClsn: {
            get(){
                return this.mode.find(f => f.name == 'clsn').value
            },
            set(v){
                this.mode = this.mode.map(m => {
                    if(m.name == 'clsn'){
                        m.value = v
                    }
                    return m
                })
            }
        },
        activeBox: {
            get(){
                return this.mode.find(f => f.name == 'box').value
            },
            set(v){
                this.mode = this.mode.map(m => {
                    if(m.name == 'box'){
                        m.value = v
                    }
                    return m
                })
            }
        },
        activeOnly: {
            get(){
                return this.mode.find(f => f.name == 'only').value
            },
            set(v){
                this.mode = this.mode.map(m => {
                    if(m.name == 'only'){
                        m.value = v
                    }
                    return m
                })
            }
        },
    },
    watch: {
        menuItem: {
            handler(v){
                if(!v.active){
                    if(this.activeClsn){
                        this.turnActiveClsn()
                    }
                    if(this.activeOnly){
                        this.turnActiveOnly()
                    }
                }
            },
            deep: true
        },
        list(v){
            if(v){
                if(this.activeClsn){
                    this.onActiveMode('clsn', false)
                }
                this.items = v
                this.mode.map(m => {
                    if(m.value){
                        this.onActiveMode(m.name, true)
                    }
                })
            }
            else{
                if(this.activeClsn){
                    this.turnActiveClsn()
                }
                if(this.activeOnly){
                    this.turnActiveOnly()
                }
            }
        }
    },
    methods: {
        onSelected(e) {
            this.choiceRegistr = false;
            this.choiceWholly = false;

            if ((e[0] == '1') || (e[1]) == '1'){
                this.choiceRegistr = true;
            }

            if ((e[0] == '2') || (e[1]) == '2'){
                this.choiceWholly = true;
            }
        },
         setFilter(filter){
            switch(filter){
                case 'one': this.one = !this.one; this.four = false; this.two = false; this.three = false;
                    break
                case 'two': this.two = !this.two; this.one = false; this.four = false; this.three = false;
                    break
                case 'three': this.three = !this.three; this.one = false; this.two = false; this.four = false;
                    break
                case 'four': this.four = !this.four; this.one = false; this.two = false; this.three = false;
                    break
                default: ;
            }
        },
        changeRegistr(){
            console.log('WORRRRk');
        },
        search(){
            if (this.message == ''){
                console.log('searchWord == none ');
                // this.ParamSearch = []
                this.$emit('update:ParamSearch', [])
                // this.list = [];
                this.$emit('update:list', [])
            }
            else{
                this.searchWord = this.message;
                this.worker.postMessage({
                    action: 'getSearch',
                    value: {TypeSearch: this.typeSearch, SearchWord: this.searchWord, choiceRegistr: this.choiceRegistr, choiceWholly: this.choiceWholly}
                })
            }
        },
        cancel(){
            this.$emit('update:ParamSearch', [])
            this.$emit('update:list', [])
            this.$emit('setOption', this.menuItem)
        },
        setResultsSearch(){
            console.log('this is setResultsSearch');
        },
        turnActiveClsn(){
            this.list = [];
            this.list.push(this.ParamSearch[this.selectedItem]['id']);

            this.activeClsn = !this.activeClsn
            if(this.activeOnly){
                this.onActiveMode('only', false)
                this.activeOnly = false
            }
            if(!this.activeClsn){
                if(this.activeBox){
                    this.turnActiveBox()
                }
            }
            this.onActiveMode('clsn', this.activeClsn)
        },
        turnActiveBox(){
            this.list = [];
            this.list.push(this.ParamSearch[this.selectedItem]['id']);

            this.activeBox = !this.activeBox
            if(this.activeOnly){
                this.onActiveMode('only', false)
                this.activeOnly = false
            }
            this.onActiveMode('box', this.activeBox)
        },
        turnActiveOnly(){

            this.list = [];
            this.list.push(this.ParamSearch[this.selectedItem]['id']);

            this.activeOnly = !this.activeOnly
            if(this.activeBox){
                this.onActiveMode('box', false)
                this.activeBox = false
            }
            if(this.activeClsn){
                this.onActiveMode('clsn', false)
                this.activeClsn = false
            }
            this.onActiveMode('only', this.activeOnly)
        },
        onActiveMode(mode, a){
            let items = this.items
            if(!items.length){
                return
            }

            switch(mode){
                case 'clsn':
                    this.onObjectsHighlight({list: items, active: a})
                    break
                case 'box':
                    this.onCreateSectionBoxForObjects({list: items, active: a})
                    break
                case 'only':
                    this.onShowObjectsOnly({list: items, active: a})
                    break
                default: ;
            }
        },
        onObjectsHighlight(v){
            this.postMessage({
                action: 'onObjectsHighlight',
                value: {
                    list: v.list,
                    active: v.active
                }
            })
        },
        onShowObjectsOnly(v){
            this.postMessage({
                action: 'onShowObjectsOnly',
                value: {
                    list: v.list,
                    active: v.active
                }
            })
        },
        onCreateSectionBoxForObjects(v){
            this.postMessage({
                action: 'onCreateSectionBoxForObjects',
                value: {
                    list: v.list,
                    active: v.active
                }
            })
        },
    },
}
</script>


