<template>
<v-container fluid fill-height text-xs-center class="m-container pb-10 pt-0">
<v-layout row wrap>
<v-flex xs12>
    <v-container fluid px-3 pt-0 class="m-container--height work-place">

        <div class="w-3 task-header" ref="header" :style="header_style">
            <div
                class="m-flex pt-6 pb-3 pselect">
                <span class="m-flex--item__auto pr-3 m-text--size13"> :</span>
                <div class="m-flex--item">
                    <v-autocomplete
                        class="pa-0 ma-0 m-text--size16"
                        v-model="project"
                        :items="projects"
                        item-text="name"
                        item-value="id"
                        persistent-hint
                        placeholder=" "
                        dense
                        @change="getTasks"
                        />
                </div>
            </div>
            <div class="task-wrap">
                <div
                    v-for="group in statusList"
                    class="task"
                    >
                    <p class="m-text--size16 ma-0 px-4 py-3">{{group.name}}</p>
                </div>
            </div>
        </div>

        <div class="task-wrap task-wrap--body">

                <Container
                    v-for="group in tasks111"
                    :key="group.name"
                    class="task"
                    group-name="1"
                    @drop="onDrop(group.group, $event)"
                    @drop-ready="onEnter($event)"
                    >
                    <!--<p class="text-gray-700 font-semibold font-sans tracking-wide text-sm">{{group.name}}</p>-->
                    <Draggable v-for="item in group.items" :key="item.task_id">
                        <div class="draggable-item">
                            <task :item="item" :editItem.sync="editItem"/>
                        </div>
                    </Draggable>
                </Container>
        </div>

        <tools v-if="editItem" :editItem.sync="editItem" :h_style="header_style"/>
        <div v-if="editItem" class="overlay" />

    </v-container>
</v-flex>
</v-layout>
</v-container>
</template>

<script>
import { Container, Draggable } from "vue-dndrop"
import Task from "./manage_tasks/Task"
import Tools from "./manage_tasks/Tools"

export default {
    name: 'ManageTasks',
    components: {
        Container,
        Draggable,
        Task,
        Tools
    },
    data(){
        return {
            project: '',
            header_style: {},
            statusList: [
                {name: '', value: 0},
                {name: ' ', value: 1},
                {name: ' ', value: 2},
                {name: '', value: 3}
            ],
            removedIndex: null,
            addedIndex: null,
            editItem: null
        }
    },
    watch: {
        removedIndex(v){
            if(v != null && this.addedIndex != null){
                this.setTasks()
            }
        },
        addedIndex(v){
            if(v != null && this.removedIndex != null){
                this.setTasks()
            }
        }
    },
    computed: {
        projects(){
            return this.$store.state.workflow.projects.map(p => {
                return {
                    id: p.id,
                    name: p.info.name
                }
            })
        },
        getOwner(){
            return this.$store.state.administration.user.id
        },
        tasks111(){
            let list = this.$store.state.workflow.taskList
            let tasks = [{
                group: 0,
                name: '',
                items: []
            },{
                group: 1,
                name: ' ',
                items: []
            },{
                group: 2,
                name: ' ',
                items: []
            },{
                group: 3,
                name: '',
                items: []
            }]
            list.map((t, i) => {
                let avatar = this.$store.state.administration.company.users
                    .find(a => a.id == t.owner)
                t.avatar = avatar
                    ? avatar.avatarSrc
                    : '',
                tasks[t.status].items.push(t)
            })
            return tasks
        }
    },
    methods: {
        checkOwner(v){
            return this.getOwner == v
        },
        setTasks(){
            let elem = this.tasks111[this.removedIndex.grp].items[this.removedIndex.ind]

            let list = this.$store.state.workflow.taskList
            list = list.map(l => {
                if(elem.task_id == l.task_id){
                    elem.status = this.addedIndex.grp
                    l.status = this.addedIndex.grp
                }
                return l
            })
            this.$store.commit('workflow/setTaskList', list)
            this.removedIndex = null
            this.addedIndex = null

            let formData = new FormData()
            let body = {
                status: elem.status
            }
            formData.append('JSON', JSON.stringify(body))
            this.$store.dispatch('workflow/updateTask', {
                type_id: 'c',
                id: elem.company_id,
                project_uuid: elem.project,
                body: formData,
                task_id: elem.task_id.toString(),
                filter: {}
            })
        },
        onDrop(v, e){
            const { removedIndex, addedIndex } = e
            if((removedIndex === null && addedIndex === null) || (removedIndex != null && addedIndex != null)) return

            if(removedIndex !== null){
                this.removedIndex = {
                    grp: v,
                    ind: removedIndex
                }
            }
            if(addedIndex !== null){
                this.addedIndex = {
                    grp: v,
                    ind: addedIndex
                }
            }
        },
        onEnter(v, g, t){
            // console.log('vbwer', v, g, t)
        },
        getTasks(v){
            this.editItem = null
            this.$store.dispatch('workflow/getTaskList', {
                type_id: 'c',
                id: this.$store.state.administration.company.id,
                project_uuid: v,
                filter: {}
            })
        }
    },
    mounted(){
        this.$nextTick(() => {
            let header = this.$refs['header']
            this.header_style = {
                top: header.getBoundingClientRect().top + 'px'
            }
        })
    },
    beforeDestroy(){
        this.$store.commit('workflow/setTaskList', [])
    }
}
</script>

