    flex: 1 0 auto;
}
.task-header{
    position: sticky;
    z-index: 1;
    background-color: #fafafa;
}
.task-header .pselect{
    width: 40%;
}

.task {
  margin: 0;
  background-color: #ebebeb;
}
.task-header .task{
    border-radius: 10px 10px 0 0;
}
.task-wrap--body .task{
    border-radius: 0 0 10px 10px;
    padding-bottom: 10px;
}
/*
