<template>
    <v-card class="m-panel__collision">
        <v-card-title class="pr-0 pl-4 m-modal--title">
             
            <v-spacer></v-spacer>
            <v-btn
                text
                icon
                @click="cancel"
                color="#7f7f7f">
                <v-icon size="20">close</v-icon>
            </v-btn>
        </v-card-title>

        <v-divider></v-divider>

        <v-card-text class="pa-4" style="background-color: #fbfbfb;">
            <div class="m-text--size14 m-text--color-black pb-3">
                       .
            </div>
            <div class="m-text--size12 m-text--color-black ">
                  
            </div>
            <v-select
                class="m-text--size14"
                dense
                label=""
                placeholder=" "
                v-model="group"
                :items="groupList"
                item-text="name"
                item-value="id"
                return-object
                append-outer-icon="trd-group-add"
                @click:append-outer="groupAdd()"
                hide-details
            />
            <div class="m-text--size12 m-text--color-black pt-4">
                 
            </div>
            <v-text-field
                v-model="ruleName"
                class="m-text--size14"
                dense
            ></v-text-field>
            <div class="m-text--size12 m-text--color-black pt-4">
                
            </div>
            <v-textarea
                placeholder=" "
                label=""
                v-model="description"
                multi-line
                rows="2">
            </v-textarea>
            <div class="m-text--size12 m-text--color-black ">
                  
            </div>
            <v-select
                class="m-text--size14"
                dense
                label=""
                placeholder=" "
                v-model="standart"
                :items="standartList"
                @change="setStandart"
                item-text="title"
                item-value="value"
            />

            <v-row dense v-for="item in getElements">
                <v-col
                    :cols="12"
                >
                    <div class="m-text--size12 m-text--color-black pt-4">
                         
                    </div>
                    <div
                        class="d-flex">
                        <v-text-field
                            :value="item.name"
                            class="m-text--size14"
                            readonly
                            dense
                        ></v-text-field>
                        <v-btn
                            text
                            @click="removeElement(item)"
                            class="m-btn">
                            <v-icon
                                size="20"
                                left>trd-trash</v-icon>
                        </v-btn>
                    </div>
                    <v-card
                        class="pa-0"
                        min-height="80"
                        outlined
                    >
                        <v-card-text class="m-text--color-black pa-0">
                            <v-list flat dense class="pa-0">
                                    <v-list-item
                                        v-for="value in item.values"
                                        class="m-panel__collision--list-item"
                                        dense
                                        :color="'primary'"
                                        :class="{'v-list-item--active': item.selected.some(s => s == value.name)}"
                                        :key="value.name"
                                        :value="value.name"
                                        >
                                        <div class="m-flex">
                                            <span
                                                class="m-flex--item"
                                                style="cursor: pointer"
                                                @click="selectParameter(item, value.name)"> {{value.name}}
                                            </span>
                                            <span
                                                class="m-flex--item m-card-list__cell select-cell">
                                                <v-select
                                                    class="m-flex--item"
                                                    hide-details
                                                    persistent-hint
                                                    :items="equalList(value.type)"
                                                    item-text="title"
                                                    item-value="value"
                                                    :value="value.equal"
                                                    @change="equalParameter($event, item, value.name)"
                                                    dense
                                                />
                                            </span>
                                            <span
                                                class="m-flex--item py-0 px-4 m-card-list__cell parameter-cell">
                                                <v-simple-checkbox
                                                    v-if="value.type == 'boolean'"
                                                    v-model="value.value"
                                                    class="ma-0"
                                                    dense
                                                    placeholder=" "
                                                    hide-details
                                                    color="primary"
                                                ></v-simple-checkbox>
                                                <v-autocomplete
                                                    v-else-if="value.type == 'list'"
                                                    class="pa-0 ma-0"
                                                    v-model="value.value"
                                                    :items="value.list"
                                                    item-text="name"
                                                    item-value="name"
                                                    persistent-hint
                                                    placeholder=" "
                                                    hide-details
                                                    dense
                                                    />
                                                <input
                                                    v-else-if="value.type == 'integer'"
                                                    class="ma-0 pa-0 m-input--noattr"
                                                    :value="value.value"
                                                    @input="onlyInteger($event, item, value.name)"
                                                ></input>
                                                <input
                                                    v-else-if="value.type == 'float'"
                                                    class="ma-0 pa-0 m-input--noattr"
                                                    :value="value.value"
                                                    @input="onlyFloat($event, item, value.name)"
                                                ></input>
                                                <v-text-field
                                                    v-else-if="value.type == 'string'"
                                                    class="ma-0 pa-0"
                                                    placeholder=" "
                                                    hide-details
                                                    :value="value.value"
                                                    @input="valueParameter($event, item, value.name)"
                                                    >
                                                </v-text-field>
                                                <v-text-field
                                                    v-else
                                                    class="ma-0 pa-0"
                                                    placeholder=" "
                                                    hide-details
                                                    :value="value.value"
                                                    @change="valueParameter($event, item, value.name)"
                                                    >
                                                </v-text-field>
                                            </span>
                                        </div>
                                    </v-list-item>
                            </v-list>
                        </v-card-text>
                    </v-card>
                    <v-card-actions>
                        <v-btn
                            text
                            @click="parametersAdd(item)"
                            color="primary"
                            class="m-btn">
                            <v-icon
                                size="20"
                                left>mdi-plus-circle-outline</v-icon> 
                        </v-btn>
                        <v-btn
                            text
                            :disabled="!item.selected.length"
                            @click="removeParameter(item)"
                            class="m-btn">
                            <v-icon
                                size="20"
                                left>trd-trash</v-icon>
                        </v-btn>
                    </v-card-actions>
                </v-col>
            </v-row>
            <v-row dense>
                <v-col
                    :cols="12"
                >
                    <div class="m-text--size12 m-text--color-black pt-4">
                         
                    </div>
                    <div
                        class="d-flex">
                        <v-text-field
                            :disabled="!standart"
                            :value="''"
                            class="m-text--size14"
                            readonly
                            dense
                        ></v-text-field>
                        <v-btn
                            :disabled="!standart"
                            text
                            @click="elementAdd(1)"
                            color="primary"
                            class="m-btn">
                            <v-icon
                                size="20"
                                left>mdi-plus-circle-outline</v-icon> 
                        </v-btn>
                    </div>
                </v-col>
            </v-row>

        </v-card-text>

        <v-divider></v-divider>
        <v-card-actions class="pa-5 px-4">
            <v-btn
                :disabled="!verify"
                outlined
                color="primary"
                class="m-btn"
                @click="saveTemplate">  
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn outlined color="normal" class="m-btn m-btn-normal" @click.stop="back"></v-btn>
            <v-btn :disabled="!verify" outlined color="primary" class="m-btn" @click="saveRules">
            </v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'
import {html2text} from '@/utils/services'

export default {
    name: 'RulesSettingsParams',
    props: ['temp', 'model', 'componentNames'],
    data(){
        return {
            group: this.temp.group || '',
            ruleName: this.temp.ruleName || '',
            list_1_selected: [],
            description: this.temp.desc || '',
            elementName: '',
            signList: [{
                title:'>',
                value: 0
            },{
                title: '=',
                value: 1
            },{
                title: '<',
                value: 2
            },{
                title: html2text('&ge;'),
                value: 3
            },{
                title: html2text('&le;'),
                value: 4
            },{
                title: html2text('&ne;'),
                value: 5
            }],
            standart: this.temp.std || '',
            standartList: [{
                    name: 'ksi',
                    value: 'ksi',
                    title: '  '
                },{
                    name: 'bim',
                    value: 'bim',
                    title: 'BIM Standart'
            }]
        }
    },
    computed: {
        groupList(){
            let collisions = this.$store.state.common.meta.collisions
            let groups = collisions ? collisions.groups || [] : []
            return groups
        },
        groupAll(){
            let collisions = this.$store.state.common.meta.collisions
            let groups = collisions ? collisions.groups || [] : []
            return groups
        },
        elements(){
            if(!this.standart){
                return []
            }
            else{
                let list = this.temp.list_1 || []
                let elements = this.temp.elements || []
                elements = elements.map(e => {
                    return {
                        ...e,
                        selected: e.selected
                            ? e.selected
                            : []
                    }
                })

                list = list
                    .filter(f => !elements.some(s => s.name == f))
                    .map(l => {
                        return {
                            name: l,
                            values: [],
                            selected: []
                        }
                    })
                return [...elements, ...list]
            }
        },
        getElements(){
            if(!this.standart){
                return []
            }
            else{
                let componentNames = this.componentNames
                    ? Object.keys(this.componentNames[this.standart])
                    : []
                return this.elements.filter(f => componentNames.some(s => s == f.name))
            }
        },
        verify(){
            return !!this.group && !!this.ruleName && !!this.elements.length
        }
    },
    methods: {
        cancel(){
            this.$emit('cancel')
        },
        onlyInteger(e, o, n){
            let value = e.target.value.replace(/[^0-9]/g, '')
            this.valueParameter(value, o, n)
        },
        onlyFloat(e, o, n){
            let value = e.target.value.replace(/[^0-9.]/g, '')
            this.valueParameter(value, o, n)
        },
        equalList(type){
            let list = []
            switch(type){
                case 'string': ;
                case 'boolean': ;
                case 'list': list = this.signList.filter(f => f.value == 1 || f.value == 5)
                    break
                case 'integer': ;
                case 'float': list = this.signList
                    break
                default: ;
            }
            return list
        },
        back(){
            this.$emit('setParams', {})
            this.setComponent()
        },
        removeElement(i){
            let update = JSON.parse(JSON.stringify(this.temp))
            let list_1 = []
            if(update.list_1){
                list_1 = update.list_1.filter(f => f != i.name)
            }
            let elements = []
            if(update.elements){
                elements = update.elements.filter(f => f.name != i.name)
            }
            update = {...update, list_1, elements}
            this.$emit('setParams', update)
        },
        setComponent(v){
            this.$emit('setComponent', v)
        },
        setStandart(v){
            let update = JSON.parse(JSON.stringify(this.temp))
            update = {...update,
                    std: v
                }
            this.$emit('setParams', update)
        },
        elementAdd(l){
            let update = JSON.parse(JSON.stringify(this.temp))
            update = {...update,
                    list: l,
                    list_1: this.elements.map(e => {
                        return e.name
                    }),
                    ruleName: this.ruleName,
                    group: this.group,
                    desc: this.description
                }
            this.$emit('setParams', update)
            this.setComponent('RulesElement')
        },
        parametersAdd(n){
            let update = JSON.parse(JSON.stringify(this.temp))
            update = {...update,
                    element: n,
                    elements: this.elements
                }
            this.$emit('setParams', update)
            this.setComponent('GetParams')
        },
        equalParameter(event, i, v){
            let update = JSON.parse(JSON.stringify(this.temp))
            let elements = update.elements
            elements = elements.map(e => {
                return {
                    ...e,
                    values: i.name == e.name
                        ? e.values.map(val => {
                            if(val.name == v){
                                val.equal = event
                            }
                            return val
                        })
                        : e.values
                }
            })

            update = {...update, elements}
            this.$emit('setParams', update)
        },
        valueParameter(event, i, v){
            let update = JSON.parse(JSON.stringify(this.temp))
            let elements = this.temp.elements
            elements = elements.map(e => {
                return {
                    ...e,
                    values: i.name == e.name
                        ? e.values.map(val => {
                            if(val.name == v){
                                val.value = event
                            }
                            return val
                        })
                        : e.values
                }
            })

            update = {...update, elements}
            this.$emit('setParams', update)
        },
        selectParameter(i, v){
            let update = JSON.parse(JSON.stringify(this.temp))
            let elements = update.elements
            elements = elements.map(e => {
                return {
                    ...e,
                    selected: i.name == e.name
                        ? e.selected.some(s => s == v)
                            ? e.selected.filter(f => f != v)
                            : [...e.selected, v]
                        : e.selected
                }
            })
            update = {...update, elements}
            this.$emit('setParams', update)
        },
        removeParameter(i){
            let update = JSON.parse(JSON.stringify(this.temp))
            let elements =update.elements
            elements = elements.map(e => {
                return {
                    ...e,
                    selected: [],
                    values: i.name == e.name
                        ? e.values.filter(f => !e.selected.some(s => s == f.name))
                        : e.values
                }
            })

            update = {...update, elements}
            this.$emit('setParams', update)
        },
        groupAdd(){
            let update = JSON.parse(JSON.stringify(this.temp))
            update = {...update,
                    ruleName: this.ruleName,
                    group: this.group,
                    desc: this.description
                }
            this.$emit('setParams', update)
            this.setComponent('GroupAdd')
        },
        saveRules(){
            this.$emit('save', {groups: this.groupAll.map(g => {
                if(g.id == this.group.id){
                    return {
                        ...g,
                        part: null,
                        children: !!this.temp.edit
                            ? g.children.map(c => {
                                if(c.id == this.temp.edit){
                                    c.part = 'params'
                                    c.std = this.standart
                                    c.name = this.ruleName
                                    c.elements = this.elements.map(e => {
                                        return {
                                            name: e.name,
                                            values: e.values
                                        }
                                    })
                                    c.desc = this.description
                                }

                                return c
                        })
                            : [...g.children, {
                            id: uuidv4(),
                            parent: g.id,
                            type: 'rules',
                            part: 'params',
                            std: this.standart,
                            name: this.ruleName,
                            status: -1,
                            elements: this.elements.map(e => {
                                return {
                                    name: e.name,
                                    values: e.values
                                }
                            }),
                            desc: this.description
                        }]
                    }
                }
                else{
                    return g
                }
            })})
            this.cancel()
        },
        saveTemplate(){
            let templates = this.$store.state.workflow.templates_collision
            let group = templates.find(f => f.name == this.group.name)
            if(group){
                let ruleName = this.ruleName
                let ruleId = uuidv4()
                group.children.map(c => {
                    if(c.name == ruleName){
                        ruleName += '_' + ruleId
                    }
                })
                group = {
                    ...group,
                    part: null,
                    children: [...group.children, {
                        id: ruleId,
                        parent: group.id,
                        type: 'rules',
                        part: 'params',
                        std: this.standart,
                        name: ruleName,
                        status: -1,
                        elements: this.elements.map(e => {
                            return {
                                name: e.name,
                                values: e.values
                            }
                        }),
                        desc: this.description
                    }]
                }
            }
            else{
                let groupId = uuidv4()
                group = {
                    id: groupId,
                    name: this.group.name,
                    status: -1,
                    part: 'geom',
                    type: 'group',
                    children: [{
                        id: uuidv4(),
                        parent: groupId,
                        type: 'rules',
                        part: 'params',
                        std: this.standart,
                        name: this.ruleName,
                        status: -1,
                        elements: this.elements.map(e => {
                            return {
                                name: e.name,
                                values: e.values
                            }
                        }),
                        desc: this.description
                    }]
                }
            }
            this.$store.dispatch('workflow/updateCollisionTemplates', [group])
            .then(res => {
                if(res.error){
                    this.$notify({
                        group: 'note',
                        type: 'error',
                        text: '  . ' + res.data.error
                    })
                }
                else{
                    this.$notify({
                        group: 'note',
                        type: 'success',
                        text: ' .'
                    })
                }
            })
        },
        enter(e){
            if(e.keyCode === 13){
                e.preventDefault()
                this.cancel()
            }
        }
    },
    mounted(){
        document.addEventListener('keyup', this.enter, false)
    },
    beforeDestroy(){
        document.removeEventListener('keyup', this.enter, false)
    }
}
</script>

<style lang="css" scoped>
.select-cell{
    border-bottom: 0;
    border-left: 1px solid rgba(0, 0, 0, 0.12);
    min-height: auto;
    padding: 0 0 0 14px;
    max-width: 56px;
}
.parameter-cell{
    border: 0;
    min-height: auto;
    height: auto;
}
</style>
