<template>
    <v-menu
      top
      right
      offset-y
      transition="slide-y-transition"
    >
        <template v-slot:activator="{ on }">
            <v-icon
                v-on="on"
                text
                icon
                class="m-icon-border"
                @mousedown.stop.pevent=""
                size="20">
                mdi-dots-horizontal
            </v-icon>
        </template>
        <v-card>
            <v-list dense>
                <v-list-item
                    v-for="(note, i) in createMenu()"
                    :key="i"
                    class="pr-10"
                    @click="action(note)"
                    >
                    <v-list-item-title
                        :class="['m-menu-title', {'m-text--color-red': note.color == 'red'}]"
                        v-text="note.title" />
                </v-list-item>
            </v-list>
        </v-card>
    </v-menu>
</template>

<script>
import { checklicense } from '@/utils/services'

export default {
    props: ['item', 'access'],
    data: () => ({
        doc: [{
            title: ' ',
            value: 'original',
            action: 'download',
            access: 2
        },{
            title: '',
            value: 'ifcxml',
            action: '',
            access: 2
        },{
            id: 3,
            title: '',
            value: 'edit',
            action: 'edit',
            access: 0
        },{
            id: 4,
            title: ' XML',
            value: 'specificationxml',
            action: 'specificationxml',
            access: 0
        },{
            title: '',
            value: 'copy',
            action: 'copy',
            access: 1
        },{
            title: '',
            value: 'replace',
            action: 'replace',
            access: 1
        },{
            id: 6,
            title: '',
            value: 'rename',
            action: 'rename',
            access: 1
        },{
            id: 7,
            title: '',
            value: 'remove',
            action: 'remove',
            access: 1
        },{
            title: ' ',
            value: 'accessright',
            action: 'accessright',
            access: 1
        },{
            title: ' ',
            value: 'versionjournal',
            action: 'versionjournal',
            access: 1
        }],
        folder: [{
            title: '',
            value: 'open',
            action: 'open',
            access: 0
        },{
            title: '',
            value: 'copy',
            action: 'copy',
            access: 1
        },{
            title: '',
            value: 'replace',
            action: 'replace',
            access: 1
        },{
            title: '',
            value: 'rename',
            action: 'rename',
            access: 1
        },{
            title: '',
            value: 'remove',
            action: 'remove',
            access: 1
        },{
            title: ' ',
            value: 'accessright',
            action: 'accessright',
            access: 1
        }]
    }),
    methods: {
        getAccess(a){
            return (this.access >> a) & 1
        },
        getIFCXMLforMenu(status){
            if(status == -1){
                return {
                    title: '  ',
                    value: 'ifcxml',
                    action: 'convertation',
                    access: 1
                }
            }
            else if(status == 1){
                return {
                    title: '  (ifcxmlRUS)',
                    value: 'ifcxml',
                    action: 'download',
                    access: 2
                }
            }
            else if(status == 3){
                return {
                    title: '   ',
                    value: 'ifcxml',
                    color: 'red',
                    action: '',
                    access: 1
                }
            }
            else if(status == 11){
                return {
                    title: '  ',
                    value: 'ifcxml',
                    action: 'errorIFCXML',
                    access: 1
                }
            }
            else{
                return {
                    title: '',
                    value: 'ifcxml',
                    action: '',
                    access: 5,
                    remove: 1
                }
            }
        },
        createMenu(){
            const type = this.item.type
            let list = []
            if(type == 'folder'){
                list = this[type]
                    .filter(f => this.getAccess(f.access)
                        && (f.value == 'accessright' ? !this.item.group : true))
            }
            else{
                list = this['doc']
                if(this.$store.state.workflow.fileTypeConversion.some(s => s == type)){
                    let statusIFCXML = this.item.tags['ifcxml.zip'] || 101
                    if(statusIFCXML == -1){
                        let error = this.item.tags['error.json'] == 1
                                ||  this.item.tags['warning.json'] == 1
                                ||  this.item.tags['classerror.json'] == 1
                                ||  this.item.tags['class.error.json'] == 1
                                ||  this.item.tags['classerror.onlyempty.json'] == 1
                        statusIFCXML = error ? 11 : statusIFCXML
                    }
                    list = list.filter(f => this.getAccess(f.access))
                        .map(m => {
                            if(m.value == 'ifcxml'){
                                if(checklicense('convertRSIM')){
                                    return this.getIFCXMLforMenu(statusIFCXML)
                                }
                                else{
                                    m.remove = 1
                                    return m
                                }
                            }
                            else if(m.value == 'specificationxml'){
                                if(!checklicense('specificationxml')){
                                    m.remove = 1
                                }
                                return m
                            }
                            else{
                                return m
                            }
                        })
                        .filter(f => !f.remove)
                }
                else if(type == 'consolidations'){
                    list = list
                        .filter(i => i.value != 'ifcxml' && (i.id == 3 || i.id == 6 || i.id == 7))
                        .filter(f => this.getAccess(f.access))
                }
                else{
                    list = list.filter(f => this.getAccess(f.access) && f.value != 'ifcxml')
                }
                if(type != 'pdf'){
                    list = list
                        .filter(i => i.id != 4)
                }
                if(this.item.status_file == 3){
                    list = list.filter(f => f.id != 3)
                }
            }
            return list
        },
        action(v){
            const type = this.item.type
            switch(v.action){
                case 'remove':
                    this.$emit('removeItem', this.item)
                    break
                case 'edit':
                    this.$emit('getDct', this.item)
                    break
                case 'open':
                    this.$emit('openFolder', this.item)
                    break
                case 'replace':
                    this.$emit('replaceItems', [this.item])
                    break
                case 'copy':
                    this.$emit('copyItems', [this.item])
                    break
                case 'rename':
                    this.$emit('renameitem', this.item)
                    break
                case 'accessright':
                    this.$emit('accessRights', this.item)
                    break
                case 'versionjournal':
                    this.$emit('versionJournal', this.item)
                    break
                case 'errorIFCXML':
                    this.$emit('errorIFCXML')
                    break
                case 'specificationxml':
                    this.$emit('onSpecificationXml', this.item)
                    break
                case 'download':
                    this.$emit('download', {
                        file: this.item,
                        type: v.value
                    })
                    break
                case 'convertation':
                    this.$emit('convertationIFCXML', this.item)
                    break
                default: return
            }
        }
    }
}
</script>
