<template>
    <span
        class="m-icon-botton"
        :class="{'m-icon-botton--disabled': disabled}"
        @click="onClick">
        <v-icon
            :color="color"
            size="20">
            <slot></slot>
        </v-icon>
    </span>
</template>

<script>
export default {
    name: 'IconBotton',
    props: {
        disabled: {
            type: Boolean,
            required: false
        },
        color: {
            type: String,
            required: false
        },
        stop: {
            type: Boolean,
            required: false
        }
    },
    methods: {
        onClick(e){
            if(this.stop){
                e.stopPropagation()
            }
            this.$emit('onClick')
        }
    }
}
</script>

<style lang="less">
    .m-icon-botton{
        display: inline-flex;
        cursor: pointer;
        &:hover *{
            color: #000;
        }
        &--disabled{
            pointer-events: none !important;
            *{
                color: #ccc !important;
                cursor: auto;
            }
        }
    }
</style>
