<template>
    <v-card class="m-overhead m-overhead--white d-block pa-4 overflow-auto">
        <v-row class="d-block">
        <div class="d-flex">
            <v-spacer />
            <v-btn
                text
                icon
                @click="cancel"
                color="#7f7f7f">
                <v-icon size="20">close</v-icon>
            </v-btn>
        </div>
    </v-row>

        <v-text-field
            :disabled="!checkOwner()"
            label=" "
            placeholder=" "
            v-model="name"
        />

        <v-select
            v-model="selectStatus"
            :items="statusList"
            item-text="name"
            item-value="value"
            persistent-hint
            label=""
            placeholder=" "
        />

        <v-autocomplete
            :disabled="!checkOwner()"
            label="  "
            placeholder=" "
            :items="managers"
            v-model="manager"
            chips
            item-text="name"
            item-value="id"
            >
            <template v-slot:selection="data">
                <v-chip
                    dark
                    v-bind="data.attrs"
                    :input-value="data.selected"
                    close
                    @click:close="removeChips()"
                >
                    <v-avatar
                        left
                    >
                        <v-img :src="data.item.avatar"></v-img>
                    </v-avatar>
                    {{ data.item.name }}
                </v-chip>
            </template>

            <template v-slot:item="data">
                <v-list-item-avatar size="30">
                    <img :src="data.item.avatar">
                </v-list-item-avatar>
                <v-list-item-content>
                    <v-list-item-title v-html="data.item.name"></v-list-item-title>
                    <v-list-item-subtitle v-html="data.item.role"></v-list-item-subtitle>
                </v-list-item-content>
            </template>
        </v-autocomplete>

        <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            min-width="290px"
            >
            <template v-slot:activator="{ on }">
                <v-text-field
                    :disabled="!checkOwner()"
                    class="mb-4"
                    v-model="computedDateFormatted"
                    label=" "
                    readonly
                    placeholder=" "
                    v-on="on"
                />
            </template>
            <v-date-picker
                ref="picker"
                v-model="date"
                :landscape="true"
                locale="ru"
                max="2050-01-01"
                min="1950-01-01"
                @change="save"
            ></v-date-picker>
        </v-menu>

        <div class="py-4" style="position: relative;">
            <label for="input-477" class="v-label v-label--active theme--light" style="left: 0px; right: auto; position: absolute; max-width: 133%; -webkit-transform: translateY(-18px) scale(0.75); transform: translateY(-18px) scale(0.75); -webkit-transform-origin: top left; transform-origin: top left;"> </label>

            <v-img
                :src="this.item.src"
            ></v-img>
        </div>

        <v-textarea
            :disabled="!checkOwner()"
            label=""
            placeholder=" "
            v-model="description"
            multi-line
            rows="3">
        </v-textarea>

        <v-card-actions class="pa-0">
            <v-spacer></v-spacer>
            <v-btn
                outlined
                color="primary"
                class="m-btn"
                @click="sendTask"
            >
            
            </v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>

export default {
    name: 'TaskCreate',
    props: ['item'],
    data(){
        return{
            name: this.item.name || '',
            selectStatus: this.item.status || null,
            statusList: [
                {name: '', value: 0},
                {name: ' ', value: 1},
                {name: ' ', value: 2},
                {name: '', value: 3}
            ],
            taskDay: '',
            date: this.item.deadline.substr(0, 10) || null,
            menu: false,
            manager: this.item.user || null,
            // screen: require('@/assets/images/screen.png'),
            no_image: 'data:image/png;base64,',
            description: this.item.dscr || ''
        }
    },
    computed: {
        getOwner(){
            return this.$store.state.administration.user.id
        },
        computedDateFormatted(){
            let date = this.formatDate(this.date)
            return date
        },
        managers(){
            let users = this.$store.state.administration.company.users || []
            let list = users.map(u => ({
                id: u.id,
                avatar: u.avatarSrc,
                name: u.first_name + ' ' + u.last_name,
                role: ''
            }))
            return list
        }
    },
    watch: {
        menu(val){
            val && setTimeout(() => (this.$refs.picker.activePicker = 'YEAR'))
        }
    },
    methods: {
        cancel(){
            this.$emit('cancel')
        },
        checkOwner(){
            return this.getOwner == this.item.owner
        },
        save(date){
            this.$refs.menu.save(date)
        },
        formatDate(date){
            if (!date) return null

            const [year, month, day] = date.split('-')
            let delta = new Date().getTimezoneOffset()

            let bd = new Date(parseInt(year), parseInt(month) - 1, parseInt(day), (Math.abs(delta) / 60))
            let mbd = bd.getTime(bd)

            this.taskDay = mbd
            return `${day}/${month}/${year}`
        },
        removeChips(){
            this.manager = null
        },
        sendTask(){
            let formData = new FormData()

            let body = {}
            if(this.name != this.item.name){
                body.name = this.name
            }
            if((new Date(this.date)).toISOString() != this.item.deadline){
                body.deadline = (new Date(this.date)).toISOString()
            }
            if(this.manager != this.item.user){
                body.user = this.manager
            }
            if(this.selectStatus != this.item.status){
                body.status = this.selectStatus
            }
            if(this.description != this.item.dscr){
                body.dscr = this.description
            }
            if(this.name != this.item.name){
                body.name = this.name
            }
            formData.append('JSON', JSON.stringify(body))

            this.cancel()

            this.$store.dispatch('workflow/updateTask', {
                type_id: 'c',
                id: this.item.company_id,
                project_uuid: this.item.project,
                body: formData,
                task_id: this.item.task_id.toString(),
                filter: {}
            })
        }
    }
}
</script>

<style>
.pic{
    position: absolute;
    top: -13px;
    right: 0;
}
.no_image{
    opacity: 0.3;
}
</style>
