<template>
    <v-container py-0>
        <v-row>
            <v-col cols="2" class="pb-0">
                <v-avatar
                    size="30">
                    <img
                        :src="getAvatar(comment)"
                    >
                </v-avatar>
            </v-col>
            <v-col cols="10" class="pb-0">
                <div class="m-comment-title">{{viewMode ? comment.user : comment.title + comment.version}}</div>
                <div class="m-comment-subtitle">{{dateToStr(comment.date)}}</div>
            </v-col>
        </v-row>
        <v-row>
            <v-col cols="2" class="pb-0 pt-2">
            </v-col>
            <v-col cols="10" class="pb-0 pt-2">
                <div
                    class="m-comment-marker--item"
                    v-for="(i, j) in comment.borders"
                    :key="'marker' + i.id"
                    >
                    <div class="m-comment-marker--value">{{i.name}}</div>
                </div>
            </v-col>
        </v-row>
        <v-row>
            <v-col cols="2">
            </v-col>
            <v-col cols="10">
                <span v-show="!editMode" class="m-comment-text">{{comment.text}}</span>
                <v-textarea
                    ref="textarea"
                    class="m-comment-no-border m-0"
                    v-show="editMode"
                    v-model="text"
                    :autofocus="editMode"
                    auto-grow
                    outlined
                    hide-details
                    multi-line
                    rows="1">
                </v-textarea>
            </v-col>
        </v-row>
        <v-row
            v-show="user.id == comment.uid ? viewMode : false">

            <v-col cols="2" class="pt-0">
            </v-col>
            <v-col cols="10" class="pt-0">
                <v-card-actions v-show="!editMode" class="pa-0">
                    <div
                        class="m-btn-text"
                        @click="edit">
                        
                    </div>
                    <div
                        class="m-btn-text"
                        @mouseup="removeComment">
                        </div>
                </v-card-actions>
                <v-card-actions v-show="editMode" class="pa-0">
                    <div
                        class="m-btn-text"
                        @click="saveChange">
                        
                    </div>
                    <div
                        class="m-btn-text"
                        @click="cancelChange">
                        
                    </div>
                </v-card-actions>
            </v-col>
        </v-row>
        <v-row v-if="comment.reply.length && viewMode">
            <v-col cols="2" class="py-0">
            </v-col>
            <v-col cols="10" class="pa-0">
                <comment-item
                    v-for="(i, j) in comment.reply"
                    :key="j"
                    :comment="i"
                    :viewMode="viewMode"/>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import { createAvatarChar } from '@/utils/services'

export default {
    name: 'CommentItem',
    props: ['comment', 'viewMode'],
    data () {
        return {
            text: '',
            reply: '',
            editMode: false
        }
    },
    computed: {
        user(){
            return this.$store.state.administration.user
        }
    },
    methods: {
        dateToStr(d){
            let date = new Date(2020, 0, 1, 0, 0, 0, 0)
            date = d + date.getTime()
            date = new Date(date)
            return date.toLocaleDateString() + ' ' + date.toLocaleTimeString()
        },
        edit(){
            this.editMode = true
            this.text = this.comment.text
        },
        cancelChange(){
            this.editMode = false
            this.text = ''
        },
        saveChange(){
            this.editMode = false
            this.$store.dispatch('comments/message', {
                id: this.comment.id,
                pid: this.comment.pid,
                action: 0,
                text: this.text
            })
            this.text = ''
        },
        removeComment(){
            this.$store.dispatch('comments/message', {
                brds: this.comment.borders.map(b => b.id),
                pid: this.comment.pid,
                id: this.comment.id,
                action: -1,
                text: this.comment.text
            })
        },
        getAvatar(i){
            if(!i.avatar){
                let users = this.$store.state.administration.company.users
                let user = users.filter(u => u.id == i.uid)
                if(user.length){
                    if(user[0].avatar){
                        return user[0].avatarSrc
                    }
                    else{
                        let name = user[0].first_name ? user[0].first_name : user[0].email
                        return createAvatarChar(name)
                    }
                }
            }
            else{
                return i.avatar
            }
        },
    }
}
</script>
