<template>
    <v-dialog :value="accessrights" scrollable max-width="914px" @input="v => v || cancel()">
        <v-card>
            <v-card-title class="pr-0 pl-4 m-modal--title"> : {{workItem.name}}
                <v-spacer></v-spacer>
                <v-btn
                    text
                    icon
                    @click="cancel"
                    color="#7f7f7f">
                    <v-icon size="20">close</v-icon>
                </v-btn>
            </v-card-title>
            <v-divider></v-divider>
            <v-card-text class="pt-5 px-4">
                <v-row>
                    <v-col cols="12" sm="12" md="12">
                        <v-autocomplete
                            label=" "
                            placeholder=" "
                            :items="managers"
                            v-model="managerList"
                            :disabled="!managers.length"
                            chips
                            hide-selected
                            return-object
                            ref="managers"
                            item-text="name"
                            item-value="id"
                            multiple
                            >
                            <template v-slot:selection="data">
                                <v-chip
                                    dark
                                    v-bind="data.attrs"
                                    :input-value="data.selected"
                                    close
                                    @click:close="removeChips(data.item)"
                                >
                                    <v-avatar
                                        left
                                    >
                                        <v-img :src="data.item.avatar"></v-img>
                                    </v-avatar>
                                    {{ data.item.name }}
                                </v-chip>
                            </template>

                            <template v-slot:item="data">
                                <v-list-item-avatar>
                                    <v-avatar
                                        size="30"
                                    >
                                        <img :src="data.item.avatar">
                                    </v-avatar>
                                </v-list-item-avatar>
                                <v-list-item-content>
                                    <v-list-item-title v-html="data.item.name"></v-list-item-title>
                                    <v-list-item-subtitle v-html="data.item.role"></v-list-item-subtitle>
                                </v-list-item-content>
                            </template>

                            <template v-slot:append-outer>
                                <v-btn
                                    outlined
                                    color="primary"
                                    class="m-btn"
                                    :disabled="!managerList.length"
                                    @click="addUsers"></v-btn>
                            </template>
                        </v-autocomplete>
                    </v-col>
                </v-row>

                <v-row>
                    <v-col cols="12" sm="12" md="12">
                        <v-data-table
                            :headers="headers"
                            :items="users"
                            disable-pagination
                            hide-default-footer
                            class="m-table-custom"
                        >
                            <template v-slot:no-data>
                                {{noData}}
                            </template>
                            <template v-slot:item.src="{ item }">
                                <v-avatar
                                    size="30"
                                    >
                                    <img
                                        class="m-card--avatar-wrap_img"
                                        :src="item.avatar"
                                        alt="avatar"
                                    >
                                </v-avatar>
                            </template>

                            <template v-slot:item.action="{ item }">
                                <v-btn
                                    v-if="item.level"
                                    text
                                    icon
                                    @click="removeUser(item)"
                                    color="#7f7f7f">
                                    <v-icon size="20">close</v-icon>
                                </v-btn>
                            </template>

                            <template v-slot:item.read="{ item }">
                                <v-switch
                                    v-model="item.permissions.can_open"
                                    color="success"
                                    inset
                                    :disabled="!item.level"
                                ></v-switch>
                            </template>
                            <template v-slot:item.edit="{ item }">
                                {{item.can_edit}}
                                <v-switch
                                    v-model="item.permissions.can_edit"
                                    color="success"
                                    inset
                                    :disabled="!item.level"
                                ></v-switch>
                            </template>
                            <template v-slot:item.download="{ item }">
                                <v-switch
                                    v-model="item.permissions.can_download"
                                    color="success"
                                    inset
                                    :disabled="!item.level"
                                ></v-switch>
                            </template>
                            <template v-slot:item.comment="{ item }">
                                <v-switch
                                    v-model="item.permissions.can_comment"
                                    color="success"
                                    inset
                                    :disabled="!item.level"
                                ></v-switch>
                            </template>
                        </v-data-table>

                    </v-col>
                </v-row>

            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions class="pa-5 px-4">
                <v-spacer></v-spacer>
                <v-btn outlined color="primary" class="m-btn" :disabled="!check.change" @click="save"></v-btn>
                <v-btn outlined color="normal" class="m-btn" @click="cancel"></v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
export default {
    name: 'AccessRights',
    props: ['accessrights', 'item'],
    data(){
        return {
            managerList: [],
            users: [],
            addedUserList: [],
            removedUserList: [],
            changedPermissionUserList: [],
            headers: [{
                    text: '',align: 'center',
                    sortable: false,
                    value: 'src',
                },
                { text: '', value: 'name' },
                { text: '', value: 'read' },
                { text: '', value: 'edit' },
                { text: ' ', value: 'download' },
                { text: '', value: 'comment' },
                { text: '', value: 'action', sortable: false },
            ],
            noData: ' ',
        }
    },
    computed: {
        managers(){
            let users = this.$store.state.administration.company.users || []
            users = users
                .filter(f => !this.users.some(s => s.permissions.id == f.id + ''))
                .map(u => ({
                    id: u.id + '',
                    avatar: u.avatarSrc,
                    email: u.email,
                    name: u.first_name + ' ' + u.last_name,
            }))
            return users
        },
        workItem(){
            let name = this.item ? this.item.name : ''
            let id = this.item ? this.item.id : ''
            let type = this.item ? this.item.type : null
            return {
                name,
                id,
                type
            }
        },
        userList(){
            return [...this.admins, ...this.users]
        },
        check(){
            let check = {
                change: false,
                changedUsers: [],
                addedUsers: [],
                removedUsers: []
            }
            let type = this.workItem.type
            if(type){
                type = type == 'folder'
                    ? 'folderPermissions'
                    : 'filesPermissions'

                let perms = this.$store.state.administration[type] || []

                perms = perms.find(fu => fu.id == this.workItem.id)
                if(perms && !perms.users){
                    perms.users = []
                }
                // if(!perms && !this.users.length){
                //     // check.change = false
                //     // return check
                // }
                // else if(!perms && this.users.length){
                //     // check.change = true
                //     // return check
                // }
                if(!perms){
                    perms = {users: []}
                }
                let admins = []
                this.$store.state.administration.groupPermissions.map(p => {
                    if(p.id == '1' || p.id == '2'){
                        admins = [...admins, ...p.users]
                    }
                })
                perms.users = [...perms.users, ...admins.filter(f => !perms.users.some(s => s.id == f.id))]

                let addedUsers = this.users.filter(f => !perms.users.some(s => s.id == f.permissions.id))
                let removedUsers = perms.users.filter(f => !this.users.some(s => f.id == s.permissions.id))
                if(addedUsers.length || removedUsers.length){
                    check.change = true
                    check.addedUsers = addedUsers.map(a => a.permissions)
                    check.removedUsers = removedUsers
                }

                let list = []
                this.users
                    .filter(f => perms.users.some(s => s.id == f.permissions.id))
                    .map(m => {
                        let item = perms.users.find(p => p.id == m.permissions.id)

                        Object.entries(item).map(([k, v]) => {
                            let str1 = v
                            let str2 = m.permissions[k]

                            // if(typeof v == "string"){
                            //     str1 = v.trim()
                            //     str2 = m.permissions[k].trim()
                            // }
                            if(typeof v == "boolean"){
                                if(str2 != str1){
                                    check.change = true
                                    check.changedUsers.push(m.permissions)
                                }
                            }

                        })
                    })
            }

            return check
        }
    },
    watch: {
        accessrights(v){
            if(v){
                document.addEventListener('keyup', this.enter, false)

                let type = this.workItem.type
                if(type){
                    type = type == 'folder'
                        ? 'folderPermissions'
                        : 'filesPermissions'
                    let perms = (this.$store.state.administration[type] || [])
                        .find(fu => fu.id == this.workItem.id)
                    let admins = []
                    this.$store.state.administration.groupPermissions.map(p => {
                        if(p.id == '1' || p.id == '2'){
                            admins = [...admins, ...p.users]
                        }
                    })
                    let users = perms
                        ? perms.users
                            ? JSON.parse(JSON.stringify(perms.users))
                            : []
                        : []
                    let companyUsers = this.$store.state.administration.company.users || []
                    let list = []
                    admins.map(a => {
                        let admin = companyUsers.find(c => c.id == a.id)
                        list.push({
                            permissions: {
                                can_comment: true,
                                can_download: true,
                                can_edit: true,
                                can_open: true,
                                email: admin.email,
                                full_name: admin.first_name + ' ' + admin.last_name,
                                id: admin.id,
                            },
                            id: admin.id + '',
                            avatar: admin.avatarSrc,
                            email: admin.email,
                            name: admin.first_name + ' ' + admin.last_name,
                            level: 0
                        })
                    })
                    list = [...list, ...companyUsers
                        .filter(c => users.some(u => u.id == c.id) && !admins.some(a => a.id == c.id))
                        .map(m => {
                            let user = users.find(l => l.id == m.id)
                            return {
                                permissions: {...user},
                                id: m.id + '',
                                avatar: m.avatarSrc,
                                email: m.email,
                                name: user.full_name.trim(),
                                level: 1
                            }
                        })
                    ]

                    this.users = list
                }
            }
            else{
                document.removeEventListener('keyup', this.enter, false)
            }
        }
    },
    methods: {
        checkRole(id){
            let level = 1
            if(this.$store.state.administration.user.id == id){
                level = 0
            }

            let permissions = this.$store.state.administration.groupPermissions

            let managers = []
            permissions.map(p => {
                if(p.id == '1' || p.id == '2'){
                    if(p.users.some(s => s.id == id)){
                        level = 0
                    }
                }
            })
            return level
        },
        cancel(){
            this.$emit('update:accessrights', false)
        },
        enter(e){
            if(e.keyCode === 13){
                e.preventDefault()
                this.save()
            }
        },
        save(){
            let type = this.workItem.type
            type = type == 'folder'
                ? 'folderPermissions'
                : 'filesPermissions'
            let promiseList = []
            let permissionFolders = this.$store.state.administration['folderPermissions']
            let permissionFiles = this.$store.state.administration['filesPermissions']
            let perms = this.$store.state.administration[type]
            let groupPerms = this.$store.state.administration.groupPermissions
            let itemPerms = perms
                ? perms.find(fu => fu.id == this.workItem.id)
                : false
            if(!perms || !itemPerms){
                perms = {
                    id: this.workItem.id, "groups": groupPerms
                }
            }
            else{
                perms = itemPerms
            }
            let action = type == 'folderPermissions'
                ? 'FolderPermissions'
                : 'FilesPermissions'

            promiseList.push(this.$store.dispatch('administration/set' + action, {
                id: this.workItem.id,
                body: {
                    ...perms,
                    users: this.users.map(u => {
                        return u.permissions
                    })
                }
            }))
            // let removedUsers = perms.users.filter(p => !this.users.some(s => s.id == p.id))
            // let addedUsers = this.users.filter(u => !perms.users.some(s => s.id == u.id))

            let list = {}
            if(this.check.addedUsers.length){
                list = this.traversUp({
                    type: this.item.type,
                    folder: this.item.type == 'folder'
                        ? this.item.id
                        : this.item.folder
                })
                permissionFolders
                    .filter(p => list.folders.some(s => s == p.id))
                    .map(m => {
                        let users = [...m.users,
                             ...this.check.addedUsers
                                .filter(f => !m.users.some(s => s.id == f.id))
                                .map(a => ({
                                    ...a,
                                    can_open: true,
                                    can_edit: false,
                                    can_download: false,
                                    can_comment: false
                                }))
                        ]
                        promiseList
                            .push(this.$store.dispatch('administration/setFolderPermissions', {
                                id: m.id,
                                body: {
                                    ...m,
                                    users: users
                                }
                            }))
                    })
            }

            list = this.traversDown(this.workItem.id)
            permissionFolders
                .filter(p => list.folders.some(s => s == p.id))
                .map(m => {
                    let users = !m.users ? [] : m.users.map(u => {
                        if(this.check.changedUsers.some(s => s.id == u.id)){
                            return this.check.changedUsers.find(c => c.id == u.id) || u
                        }
                        else{
                            return u
                        }
                    })
                    users = users.filter(u => !this.check.removedUsers.some(s => s.id == u.id))
                    users = [
                        ...users.filter(f => !this.check.addedUsers.some(s => s.id == f.id)),
                        ...this.check.addedUsers
                    ]
                    promiseList
                        .push(this.$store.dispatch('administration/setFolderPermissions', {
                            id: m.id,
                            body: {
                                ...m,
                                users: users
                            }
                        }))
                })
            permissionFiles
                .filter(p => list.files.some(s => s == p.id))
                .map(m => {
                    let users = !m.users ? [] : m.users.map(u => {
                        if(this.check.changedUsers.some(s => s.id == u.id)){
                            return this.check.changedUsers.find(c => c.id == u.id) || u
                        }
                        else{
                            return u
                        }
                    })
                    users = users.filter(u => !this.check.removedUsers.some(s => s.id == u.id))
                    users = [
                        ...users.filter(f => !this.check.addedUsers.some(s => s.id == f.id)),
                        ...this.check.addedUsers
                    ]
                    promiseList
                        .push(this.$store.dispatch('administration/setFilesPermissions', {
                            id: m.id,
                            body: {
                                ...m,
                                users: users
                            }
                        }))
                })

    // return
    //         this.$store.dispatch('administration/set' + action, {
    //             id: this.workItem.id,
    //             body: {
    //                 ...perms,
    //                 users: this.users.map(u => {
    //                     return u.permissions
    //                 })
    //             }
    //         })
            Promise.all(promiseList)
            .then(res => {
                this.$store.dispatch('administration/getFoldersPermissions')
                this.$store.dispatch('administration/getFilesPermissions')
            })
            this.cancel()
        },
        traversDown(id){
            let foldersMap = this.$store.state.workflow.foldersMap
            let folder = foldersMap.find(f => f.id == id)
            let list = {
                folders: [],
                files: []
            }
            if(folder && folder.files){
                list.files = folder.files.map(f => f.id)
            }
            foldersMap.filter(f => f.parent == id)
                .map(m => {
                    list.folders.push(m.id)

                    let stack = this.traversDown(m.id)
                    list.folders = [...list.folders, ...stack.folders]
                    list.files = [...list.files, ...stack.files]
                })
            return list
        },
        traversUp(item){
            let foldersMap = this.$store.state.workflow.foldersMap
            let folder = null
            let parent = null
            if(item.type == 'folder'){
                folder = foldersMap.find(f => f.id == item.folder)
                parent = foldersMap.find(f => f.id == folder.parent)
            }
            else{
                parent = foldersMap.find(f => f.id == item.folder)
            }
            let list = {
                folders: [],
                files: []
            }
            if(parent){
                if(!parent.group){
                    let stack = this.traversUp({
                        type: 'folder',
                        folder: parent.id
                    })
                    list.folders = [parent.id, ...stack.folders]
                }
            }

            return list
        },
        getAvatar(i){
            return i ? i.avatar : ''
        },
        removeUser(i){
            this.users = this.users.filter(u => u.permissions.id != i.permissions.id)
            this.addedUserList = this.addedUserList.filter(u => u.permissions.id != i.permissions.id)
        },
        removeChips(i){
            this.managerList = this.managerList.filter(m => m.id != i.id)
        },
        addUsers(){
            let managerList = this.managerList.map(m => ({
                ...m,
                level: this.checkRole(m.id),
                permissions:{
                    id: m.id,
                    email: m.email,
                    full_name: m.name.trim(),
                    can_comment: false,
                    can_download: false,
                    can_edit: false,
                    can_open: false
                }
            }))
            this.addedUserList = [
                ...this.addedUserList,
                ...managerList
            ]
            this.users = [
                ...this.users,
                ...managerList
            ]
            this.managerList = []
            this.$refs.managers.blur()
        }
    },
    created(){
        this.users = []
        this.managerList = []
    }
}
</script>
