<template>
    <v-card class="m-panel__collision">
        <v-card-title class="pr-0 pl-4 m-modal--title">
             
            <v-spacer></v-spacer>
            <v-btn
                text
                icon
                @click="cancel"
                color="#7f7f7f">
                <v-icon size="20">close</v-icon>
            </v-btn>
        </v-card-title>

        <v-divider></v-divider>

        <v-card-text class="px-4" style="background-color: #fbfbfb;">


            <div class="m-text--size12 m-text--color-black pt-4">
                 
            </div>
            <v-text-field
                v-model="name"
                class="m-text--size14"
                dense
            ></v-text-field>
        </v-card-text>

        <v-divider></v-divider>
        <v-card-actions class="pa-5 px-4">
            <v-spacer></v-spacer>
            <v-btn outlined color="normal" class="m-btn m-btn-normal" @click.stop="setComponent"></v-btn>
            <v-btn outlined color="primary" class="m-btn" @click="save">
            </v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'
export default {
    name: 'GroupAdd',
    props: ['temp'],
    data(){
        return {
            name: this.temp.editName || ''
        }
    },
    computed: {
        getGroups(){
            let collisions = this.$store.state.common.meta.collisions
            let groups = collisions ? collisions.groups || [] : []
            return groups
        }
    },
    methods: {
        cancel(){
            this.$emit('cancel')
        },
        setComponent(){
            this.$emit('setComponent')
        },
        save(){
            // let file
            // if(this.model.version == null){
            //     file = this.model.files[0]
            // }
            // else{
            //     file = this.model.files.find(i => i.id == this.model.version) || this.model.files[0]
            // }
            let update = []
            if(!!this.temp.edit){
                update = this.getGroups.map(g => {
                    if(g.id == this.temp.edit){
                        if(!this.getGroups.some(s => s.name == this.name)){
                            g.name = this.name
                        }
                    }
                    return g
                })
            }
            else{
                let ID = uuidv4()
                let name = this.getGroups.some(s => s.name == this.name)
                    ? this.name + '_' + ID
                    : this.name
                update = [...this.getGroups, {
                    id: ID,
                    name: name,
                    status: -1,
                    part: null,
                    type: 'group',
                    children: []
                }]
            }
            this.$emit('save', {groups: update})
            this.setComponent()
        },
        enter(e){
            if(e.keyCode === 13){
                e.preventDefault()
                this.cancel()
            }
        }
    },
    mounted(){
        document.addEventListener('keyup', this.enter, false)
    },
    beforeDestroy(){
        document.removeEventListener('keyup', this.enter, false)
    }
}
</script>
