<template>
    <v-menu
      top
      right
      offset-y
      transition="slide-y-transition"
    >
        <template v-slot:activator="{ on }">
            <v-icon
                v-on="on"
                text
                icon
                class="m-folder-btn"
                @mousedown.stop=""
                size="20">
                mdi-dots-horizontal
            </v-icon>
        </template>
        <v-card>
            <v-list dense>
                <v-list-item
                    v-for="(note, i) in createMenu()"
                    :key="i"
                    @click="action(note)"
                    >
                    <v-list-item-title
                        class="m-menu-title"
                        v-text="note.title" />
                </v-list-item>
            </v-list>
        </v-card>
    </v-menu>
</template>

<script>
    export default {
        props: ['node', 'editNode'],
        data: () => ({
            notes: [{
                title: '',
                value: 'rename',
                action: 'editFolderAction',
                access: 1
            },{
                title: '',
                value: 'remove',
                action: 'removeFolderAction',
                access: 1
            },{
                title: ' ',
                value: 'folder',
                action: 'newFolderAction',
                access: 0
            },{
                title: ' ',
                value: 'access',
                action: 'accessFolderAction',
                access: 1
            },]
        }),
        methods: {
            action(v){
                let isFile = {
                    parent: 0,
                    file: false,
                    group: false,
                    lvl: 1
                }
                this.$emit('update:editNode', this.node)
                this.$emit(v.action, true)
            },
            getAccess(a){
                return (this.node.data.access >> a) & 1
            },
            createMenu(){
                return this.notes.filter(f => this.getAccess(f.access)
                    && (f.value == 'access' ? !this.node.data.group : true))
            }
        }
    }
</script>
