<template>
    <v-menu
        bottom
        left
        offset-y
        transition="slide-y-transition"
        >
        <template v-slot:activator="{ attrs, on }">
            <v-btn
                class="toolbar-items"
                v-bind="attrs"
                v-on="on"
                icon
            >
                <v-avatar
                    size="30"
                    >
                    <img
                        class="m-card--avatar-wrap_img"
                        :src="avatar.avatar"
                        alt="avatar"
                    >
                </v-avatar>
            </v-btn>
        </template>
        <v-card>
            <v-list-item class="grow">
                <v-list-item-avatar size="30">
                    <v-img
                        class="m-card--avatar-wrap_img"
                        :src="avatar.avatar"
                    ></v-img>
                </v-list-item-avatar>
                <v-list-item-content>
                    <v-list-item-title class="m-text--size14 py-1">
                        {{user.first_name + ' ' + user.last_name}}
                    </v-list-item-title>
                    <v-list-item-subtitle class="m-text--size12">
                        {{user.email}}
                    </v-list-item-subtitle>
                </v-list-item-content>
            </v-list-item>
            <v-divider></v-divider>

            <v-list dense>
                <v-list-item
                    v-for="(note, i) in createMenu()"
                    :key="i"
                    @click.prevent="onClick(note.link)"
                    >
                    <v-list-item-title v-text="note.title" />
                </v-list-item>
            </v-list>
        </v-card>
    </v-menu>
</template>

<script>
    export default {
        data: () => ({
            notes: [{
                    title: '',
                    link: '/administration/account',
                    access: []
                },
                {
                    title: '',
                    link: '/administration/settings',
                    access: ['1', '2']
                },
                {
                    title: '',
                    link: 'logout',
                    access: []
                }
            ],
        }),
        computed: {
            avatar(){
                return this.$store.state.administration.userAvatar
            },
            user(){
                return this.$store.state.administration.user
            },
        },
        methods: {
            createMenu(){
                let list = this.notes.filter(n => !n.access.length
                    || n.access.some(s => s == this.user.role))
                return list
            },
            onClick(link){
                if(link == 'logout'){
                    this.$store.dispatch('auth/logout')
                }
                else if(this.$route.path == link){
                    return
                }
                else{
                    this.$router.push(link)
                }
            },
        }
    }
</script>
