<template>
<v-container fluid fill-height text-xs-center class="m-container pb-10 pt-0">
<v-layout row wrap>
<v-flex xs12>

    <v-container
        ref="wrap"
        fluid
        :fill-height="!projectList.length"
        px-3
        pt-0
        pb-10>
        <v-layout
            v-if="!projectList.length"
            fluid
            fill-height
            column
            align-center
            justify-center
            >
            <template v-if="getAccess(getPermission, 'and', [0])">
                <div class="m-dummy--title-2">    ,  !</div>
                <div class="py-3">
                    <v-btn
                        v-show="getAccess(getPermission, 'and', [0])"
                        outlined
                        color="primary"
                        @click.stop="onProject(false)"
                        class=" m-btn--text_icon mr-4">
                        <v-icon
                            size="16"
                            left>mdi-plus-circle-outline</v-icon> 
                    </v-btn>
                    <span v-if="checklicense('uploadProject')"></span>
                    <v-btn
                        v-if="getAccess(getPermission, 'and', [0]) && checklicense('uploadProject')"
                        text
                        @click.stop="inputFiles"
                        class="ma-0 m-btn--text_icon">
                        <v-icon
                            size="16"
                            left>icon-upload</v-icon> 
                    </v-btn>
                </div>
                <div class="m-dummy--link-info mt-2">  ?</div>
            </template>
            <template v-else>
                <div class="m-dummy--title-2">      .</div>
            </template>
        </v-layout>

        <v-toolbar
            v-if="projectList.length"
            flat
            height="auto"
            class="m-table-toolbar">
            <v-text-field
                v-model="search"
                prepend-icon="search"
                placeholder="..."
                single-line
                hide-details
                class="m-table-toolbar--search ml-2"
                ></v-text-field>

            <v-spacer></v-spacer>
            <input
                type="file"
                v-show="false"
                ref="input-files"
                name="fileXml"
                required
                @change="onImportXml">
            <v-btn
                v-if="getAccess(getPermission, 'and', [0]) && checklicense('uploadProject')"
                text
                @click.stop="inputFiles"
                class="ma-0 m-btn--text_icon">
                <v-icon
                    size="16"
                    left>icon-upload</v-icon> 
            </v-btn>
            <v-btn
                v-show="getAccess(getPermission, 'and', [0])"
                outlined
                color="primary"
                @click.stop="onProject(false)"
                class="ma-0 m-btn--text_icon ml-2">
                <v-icon
                    size="16"
                    left>mdi-plus-circle-outline</v-icon> 
            </v-btn>
        </v-toolbar>

        <v-data-table
            v-if="projectList.length"
            :headers="headers"
            :items="projectList"
            fixed-headerw
            heightq="200"
            :show-select="getAccess(getPermission, 'or', [1, 7])"
            v-model="selected"
            :search="search"
            disable-pagination
            hide-default-footer
            class="m-table-custom"
        >

            <template v-slot:item.name="{ item }">
                <span
                    class="m-text--hover"
                    @click="getProject(item)"
                    >{{item.info.name}}
                </span>
            </template>

            <template v-slot:item.manager="{ item }">
                {{item.info.in_charge_name}}
            </template>

            <template v-slot:item.client="{ item }">
                {{item.info.client}}
            </template>

            <template v-slot:item.date="{ item }">
                {{getDate(item.info.created_at)}}
            </template>

            <template v-slot:item.count_files="{ item }">
                {{item.info.number_of_files}}
            </template>

            <template v-slot:item.action="{ item }">
                <action-menu
                    v-if="!isSelected(item.id) && getAccess(getPermission, 'or', [1, 7])"
                    @onProject="onProject(item)"
                    @onProjectDescription="onProjectDescription"
                    @onCreateXml="onCreateXml"
                    @getProject="getProject"
                    @removeProject="removeProject"
                    :permission="getPermission"
                    :item="item"
                />
                <div
                    v-else
                    :style="{width: '34px'}">
                </div>
            </template>
            <template
                slot="pageText"
                slot-scope="props"
                >
                {{ props.pageStart }} - {{ props.pageStop }}  {{ props.itemsLength }}
            </template>
        </v-data-table>

        <edit-project
            :editProject.sync="editProject"
            :item="itemCurrent"
            />
        <project-description
            :projectDescription.sync="projectDescription"
            />
        <create-xml
            :create_xml.sync="create_xml"
            :item="itemCurrent"
            @createXml="createXml"
            />

        <v-card
            class="m-table-custom--action"
            :style="cardActionStyle"
            v-show="(selected.length == 1 && getAccess(getPermission, 'and', [7]))
                || (selected.length && getAccess(getPermission, 'and', [1]))"
            >
            <v-card-actions>
                <v-btn
                    v-if="selected.length == 1 && getAccess(getPermission, 'and', [7])"
                    text
                    color="primary"
                    class="m-btn"
                    @click="onProject(false)">
                    <v-icon size="20" class="mr-1" color="#0070e0">mdi-information-outline</v-icon> </v-btn>
                <v-btn
                    v-if="getAccess(getPermission, 'and', [1])"
                    text
                    color="primary"
                    class="m-btn"
                    @click="removeProjectSelected">
                    <v-icon size="20" class="mr-1" color="#0070e0">mdi-trash-can-outline</v-icon>
                    
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-container>
</v-flex>
</v-layout>
</v-container>
</template>

<script>
import { getAccess, checklicense } from '@/utils/services'
import EditProject from "./workflow_list/EditProject"
import ProjectDescription from "./workflow_list/ProjectDescription"
import ActionMenu from "./workflow_list/ActionMenu"
import CreateXml from './workflow_list/CreateXml'
import { v4 as uuidv4 } from 'uuid'

export default {
    name: 'WorkflowList',
    components: {
        EditProject,
        ProjectDescription,
        ActionMenu,
        CreateXml
    },
    data(){
        return {
            search: '',
            all_row: false,
            table_type_list: [{
                name: 'list',
                icon: 'mdi-format-list-bulleted'
            },{
                name: 'grid',
                icon: 'mdi-view-grid'
            }],
            headers: [
                { text: ' ', value: 'name', grid: false },
                { text: '  ', value: 'manager', grid: true },
                { text: '', value: 'client', grid: true },
                { text: ' ', value: 'date', align: 'left', grid: true },
                { text: '. ', value: 'count_files', grid: true, align: 'center'},
                { text: '', value: 'action', sortable: false, grid: false },
                ],
            projects: [],
            selected: [],
            newProject: false,
            aboutProject: false,
            editProject: false,
            itemCurrent: null,
            type_id: '',
            company_id: '',
            create_xml: false,
            projectDescription: false
        }
    },
    computed: {
        filteredProjects(){
            const s = this.search.toLowerCase()
            return this.projectList.filter(n => n.name.toString().toLowerCase().includes(s))
        },
        projectList(){
            let list = this.$store.state.workflow.projects
            return list
        },
        cardActionStyle(){
            let width = 0
            let left = 0
            if(this.selected.length){
                let box = this.$refs['wrap'].getBoundingClientRect()
                width = box.width
                left = box.x
            }
            return {width: width + 'px', left: left + 'px'}
        },
        getPermission(){
            let user = this.$store.state.administration.user
            return user.permission
        }
    },
    methods: {
        checklicense(v){
            return checklicense(v)
        },
        getAccess(a, b, c){
            return getAccess(a, b, c)
        },
        getAccessSelected(power, ar){
            if(!this.selected.length){
                return true
            }
            else{
                return !!getAccess(this.getPermission, power, ar)
            }
        },
        onCreateXml(i){
            console.log('open create xml')
            this.itemCurrent = i
            this.create_xml = true
        },
        onProjectDescription(){
            this.projectDescription = true
        },
        createXml(i){
            console.log('create xml')
        },
        getDate(d){
            let options = {year: '2-digit', month: '2-digit', day: '2-digit', hour: '2-digit', minute:  '2-digit'}
            return (new Date(parseInt(d) * 1000)).toLocaleDateString("ru-RU", options)
        },
        isSelected(id){
            let i = this.selected.filter(i => id == i.id)
            return i.length ? true : false
        },
        addProject(){
            this.newProject = true
        },
        onProject(i){
            if(!i){
                this.itemCurrent = this.getSelectedProject()
            }
            else{
                this.itemCurrent = i
            }
            this.editProject = true
        },
        getSelectedProject(){
            if(this.selected.length == 1){
                let item = this.projectList.filter(i => i.id == this.selected[0].id)[0]
                return item
            }
            else{
                return null
            }
        },
        getProject: function(i){
            this.$store.commit('workflow/setActiveGroup', false)
            this.$store.commit('workflow/setActiveFolder', false)
            this.$router.push('/workflow/' + i.id + '/list')
        },
        // selectItem(item){
        //     let exist = false
        //     let index = 0
        //     this.selected.map((i, j) => {
        //         if(i.id == item.id){
        //             exist = true
        //             index = j
        //         }
        //     })
        //
        //     if(!exist){
        //         this.selected.push(item)
        //     }
        //     else{
        //         this.selected.splice(index, 1)
        //     }
        // },
        removeProject: function(i){
            this.$store.dispatch('workflow/removeProject', {uuid: i.id})
            .then(() => {
                this.$store.dispatch('workflow/getProjects', {
                        type_id: this.type_id,
                        id: this.company_id
                    })
            })
            .then(() => {
                this.$notify({
                    group: 'note',
                    type: 'success',
                    text: ' ' + i.info.name + ' .'
                })
            })
        },
        removeProjectSelected: function(){
            let list = this.selected
            let listPromises = []
            list.map(i => {
                listPromises.push(this.$store.dispatch('workflow/removeProject', {uuid: i.id})
                .then(() => {
                    this.$notify({
                        group: 'note',
                        type: 'success',
                        text: ' ' + i.info.name + ' .'
                    })
                }))
            })
            Promise.all(listPromises)
            .then(() => {
                this.$store.dispatch('workflow/getProjects', {
                        type_id: this.type_id,
                        id: this.company_id
                    })
                this.selected = []
            })
        },
        inputFiles(){
            this.$refs['input-files'].click()
        },
        onImportXml(e){
            let files = e.target.files || e.dataTransfer.files
            let item
            for(let x in files){
                if(!isNaN(x)){
                    // let file = {
                    //     id: new Date().getTime(),
                    //     selected: false,
                    //     item: files[x]
                    // }
                    item = files[x]
                }
            }

            let formData = new FormData()
            let uuid = uuidv4()
            formData.append('file', item)
            formData.append('name', item.name)
            // formData.append('project_id', this.project_id)
            formData.append('is_attachment', true)
            formData.append('uuid', uuid)

            this.$store.dispatch('common/setUploadNote', [{
                    name: item.name,
                    uuid: uuid,
                    action: 'upload',
                    pushin: true}]
            )
            .then(() => {
                return this.$store.dispatch('common/importXml', formData)
            })
            .then(res => {
                return new Promise(resolve => {
                    const interval = setInterval(() => {
                        this.$store.dispatch('common/checkImportXml', {task_id: res.TaskID})
                        .then(res => {
                            if(res.Status != 1){
                                clearInterval(interval)
                                resolve(res)
                            }
                        })
                    }, 2000)
                })
            })
            .then(res => {
                console.log(res)
                this.$store.dispatch('common/setUploadNote', [{
                        uuid: uuid,
                        action: 'upload',
                        pushin: false}]
                )
            })
            .then(() => {
                this.$store.dispatch('workflow/getProjects', {
                    type_id: this.type_id,
                    id: this.company_id
                })
            })

        },
    },
    created(){
        this.type_id = 'c'
        this.company_id = this.$store.state.administration.company.id
        this.$store.dispatch('administration/getGroupPermissions', null)
        .then(() => {
            this.$store.dispatch('workflow/getProjects', {
                type_id: this.type_id,
                id: this.company_id
            })
        })
    }
}
</script>
