<template>
    <v-layout app>
        <app-bar :part="part"/>
        <sidebar :part="part"/>
        <core-view/>
    <!--
        <v-fab-transition>
            <v-btn
                v-scroll="onScroll"
                v-show="fab"
                 transition="fade-transition"
                fab
                fixed
                bottom
                right
                color="primary"
                @click="toTop"
                >
                <v-icon>keyboard_arrow_up</v-icon>
            </v-btn>
        </v-fab-transition>
    -->
    </v-layout>
</template>

<script>
import sidebar from "./sidebar/sidebar.js"
import AppBar from '@/components/core/AppBar'
import Sidebar from '@/components/core/Sidebar'
import CoreView from '@/components/core/CoreView'

    export default {
        name: 'Layout',
        data: () => ({
            fab: false,
            part: {}
        }),
        watch: {
            '$route' (to, from){
                this.part = this.createNav()
            }
        },
        components: {
            AppBar,
            Sidebar,
            CoreView
        },
        computed: {
            getRole(){
                return this.$store.state.administration.user.role
            }
        },
        created(){
            this.part = this.createNav()
        },
        methods: {
            onScroll (e) {
                if (typeof window === 'undefined') return
                const top = window.pageYOffset ||   e.target.scrollTop || 0
                this.fab = top > 20
            },
            toTop () {
                this.$vuetify.goTo(0)
            },
            createNav: function(){
                let part = Object.assign({}, sidebar[this.$route.meta.part])

                let keys = Object.keys(part)

                if(keys.includes('clone')){
                    let clone = Object.assign({}, sidebar[part.clone])
                    keys.map(i => {
                        clone[i] = part[i]
                    })
                    part = Object.assign({}, clone)
                }

                part.list = part.list.filter(l => !l.access_list.length
                        || l.access_list.some(s => this.getRole == s)
                )

                let crumbs = []

                this.$route.matched.map(m => {
                    if(Object.prototype.hasOwnProperty.call(m.meta, 'crumbs')){
                        crumbs.push({
                            title: m.meta.crumbs,
                            path: m.path
                        })
                    }
                })
                //let title = crumbs.join(' / ')

                this.$store.commit('toolbar/setTitle', crumbs)

                return part
            }
        }
    }
</script>
