<template>
<v-container fluid fill-height text-xs-center class="m-container pb-10 pt-0">
<v-layout row wrap>
<v-flex xs12>
    <v-container fluid px-3 pb-10 pt-0 class="m-container" ref="wrap">

        <v-toolbar flat height="auto" class="m-table-toolbar">
            <v-text-field
                v-model="search"
                prepend-icon="search"
                placeholder="..."
                single-line
                hide-details
                class="m-table-toolbar--search ml-2"
                ></v-text-field>

            <v-spacer></v-spacer>
            <v-btn
                text
                @click.stop="onRemove(null, 'trash')"
                class="ma-0 m-btn--text_icon">
                <v-icon
                    size="16"
                    left>mdi-trash-can-outline</v-icon> 
            </v-btn>
        </v-toolbar>

        <v-data-table
            :headers="headers"
            :items="getRemoved"
            show-select
            v-model="selected"
            @toggle-select-all="selectRow"
            :search="search"
            disable-pagination
            hide-default-footer
            class="m-table-custom"
        >
            <template v-slot:no-data>
                {{noData}}
            </template>

            <template v-slot:item.data-table-select="{ item, isSelected, select }">
                <v-simple-checkbox
                    :value="isSelected"
                    @input="select($event)">
                </v-simple-checkbox>
            </template>

            <template v-slot:item.src="{ item }">
                <v-icon
                    size="30">{{item.type == 'folder'
                        ? 'icon-folder'
                        : item.type == 'type'
                            ? 'ft-unknown'
                            : 'ft-' + item.type}}
                </v-icon>
            </template>

            <template v-slot:item.name="{ item }">
                <span class="m-table--name">
                    <span>
                        <v-tooltip top>
                            <template v-slot:activator="{ on, attrs }">
                                <span
                                    v-bind="attrs"
                                    v-on="on">
                                    {{item.name}}
                                </span>
                            </template>
                            {{item.name}}
                        </v-tooltip>
                    </span>
                </span>
            </template>

            <template v-slot:item.status="{ item }">
                <span>
                    {{status[item.status]}}
                </span>
            </template>

            <template v-slot:item.action="{ item }">
                <action-menu
                    v-if="!checkSelected(item)"
                    @onRemove="onRemove"
                    @onRestore="onRestore"
                    :item="item"
                />
            </template>
        </v-data-table>

        <v-card
            class="m-table-custom--action"
            :style="cardActionStyle"
            v-show="selected.length"
            >
            <v-card-actions>
                <v-btn
                    text
                    color="primary"
                    class="m-btn"
                    @click="onRestoreSelected">
                    <v-icon size="20" class="mr-1" color="#0070e0">mdi-trash-can-outline</v-icon>
                    
                </v-btn>
                <v-btn
                    text
                    color="primary"
                    class="m-btn"
                    @click="onRemoveSelected">
                    <v-icon size="20" class="mr-1" color="#0070e0">mdi-trash-can-outline</v-icon>
                    
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-container>
</v-flex>
</v-layout>
</v-container>
</template>

<script>
import { sortObj } from '@/utils/services'
import ActionMenu from "./removed_files/ActionMenu"
export default {
    name: 'RemovedFiles',
    components: {
        ActionMenu
    },
    props: ['id'],
    data(){
        return {
            search: '',
            headers: [
                {
                    text: '',
                    align: 'center',
                    sortable: false,
                    value: 'src',
                    grid: false
                },
                { text: ' ', value: 'name', grid: true },
                { text: '', value: 'projectName', grid: false },
                { text: ' ', value: 'trash_time', grid: false },
                { text: '', value: 'deleted_by', grid: false },
                { text: ' ', value: 'upload_date', grid: false },
                { text: '', value: 'status', grid: true },
                { text: '', value: 'action', sortable: false, grid: false },
                ],
            status: {
                '0': ' ',
                '1': ' ',
                '2': '',
                '3': ''
            },
            noData: ' ',
            selected: [],
            items: [],
            status: {
                '-1': '',
                '0': ' ',
                '1': ' ',
                '2': '',
                '3': ''
            },
            actionList: ['trash', 'project', 'folder', 'file']
        }
    },
    computed: {
        getRemoved(){
            let list = []
            let state = this.$store.state
            let projects = state.workflow.projects
            let users = state.administration.company.users
            let dx = 1
            this.items.map(i => {
                projects.map(p => {
                    if(p.id == i.project){
                        let project = {
                            projectId: p.id,
                            projectName: p.info.name
                        }
                        if(i.TrashFolder.folders){
                            i.TrashFolder.folders.map(f => {
                                let options = {year: '2-digit', month: '2-digit', day: '2-digit', hour: '2-digit', minute:  '2-digit'}
                                let created_at = new Date(f.created_at)
                                let trash_time = new Date(f.trash_time)
                                let user = users.find(u => u.id == f.deleted_by + '')
                                user = user
                                    ? user.first_name + ' ' + user.last_name
                                    : ''
                                let item = {
                                    ...project,
                                    ...f,
                                    type: 'folder',
                                    trash_time: trash_time.toLocaleDateString("ru-RU", options),
                                    upload_date: created_at.toLocaleDateString("ru-RU", options),
                                    deleted_by: user
                                }
                                list.push(item)
                            })
                        }
                        if(i.TrashFolder.files){
                            i.TrashFolder.files.map(f => {
                                let options = {year: '2-digit', month: '2-digit', day: '2-digit', hour: '2-digit', minute:  '2-digit'}
                                let created_at = new Date(f.created_at)
                                let trash_time = new Date(f.trash_time)
                                let user = users.find(u => u.id == f.deleted_by + '')
                                user = user
                                    ? user.first_name + ' ' + user.last_name
                                    : ''

                                let arName = f.name.split('.')
                                let type = arName.splice(-1, 1)[0] || 'type'
                                if(f.consolidations){
                                    type = 'consolidations'
                                }
                                let item = {
                                    ...project,
                                    ...f,
                                    type: type,
                                    trash_time: trash_time.toLocaleDateString("ru-RU", options),
                                    upload_date: created_at.toLocaleDateString("ru-RU", options),
                                    deleted_by: user
                                }
                                list.push(item)
                            })
                        }
                    }
                })
            })
            return list
        },
        cardActionStyle(){
            let width = 0
            let left = 0
            if(this.selected.length){
                let box = this.$refs['wrap'].getBoundingClientRect()
                width = box.width
                left = box.x
            }
            return {width: width + 'px', left: left + 'px'}
        }
    },
    methods: {
        checkSelected(i){
            return this.selected.some(s => s.id == i.id)
        },
        selectRow(e){
            let list = e.items
            list = list.filter(i => i.inwork !== true)

            if(this.selected.length != list.length){
                this.selected = list
            }
            else{
                this.selected = []
            }
        },
        onRestore(item){
            let type = item.type == 'folder'
                ? 'folder'
                : 'file'
            this.$store.dispatch('workflow/restoreFiles', {
                fileName: item.name,
                fileId: item.id,
                uuid: item.projectId,
                fileType: type
            })
            .then(res => {
                this.$notify({
                    group: 'note',
                    type: res.status ? 'success' : 'error',
                    text: res.status
                        ? res.fileType == 'folder'
                            ? ' ' + res.name + '<br/>.'
                            : ' ' + res.name + '<br/>.'
                        : res.name + '<br/>' + res.text
                })
            })
            .then(res => {
                return this.$store.dispatch('workflow/getRemovedFiles')
            })
            .then(res => {
                this.items = res
            })
        },
        onRestoreSelected(){
            let listPromises = []
            this.selected.map(s => {
                let type = s.type == 'folder'
                    ? 'folder'
                    : 'file'
                listPromises.push(
                    this.$store.dispatch('workflow/restoreFiles', {
                        fileName: s.name,
                        fileId: s.id,
                        uuid: s.projectId,
                        fileType: type
                    })
                )
            })

            Promise.all(listPromises)
            .then(res => {
                res.map(r => {
                    this.selected = []
                    this.$notify({
                        group: 'note',
                        type: r.status ? 'success' : 'error',
                        text: r.status
                            ? res.fileType == 'folder'
                                ? ' ' + res.name + '<br/>.'
                                : ' ' + res.name + '<br/>.'
                            : r.name + '<br/>' + r.text
                    })
                })
                return true
            })
            .then(res => {
                return this.$store.dispatch('workflow/getRemovedFiles')
            })
            .then(res => {
                this.items = res
            })
        },
        onRemove(item, action){
            if(action == 'trash' && !this.items.length){
                return true
            }
            this.removeItem(item, action)
            .then(res => {
                return this.$store.dispatch('workflow/getRemovedFiles')
            })
            .then(res => {
                this.items = res
            })
        },
        removeItem(item, action){
            action = this.actionList.some(s => s == action) ? action : 'file'
            return new Promise(resolve => {
                this.$store.dispatch('workflow/removeRemovedFiles', {
                    fileName: item ? item.name : null,
                    fileId: item ? item.id : null,
                    uuid: item ? item.projectId : null,
                    fileType: item
                        ? item.type == 'folder'
                            ? item.type
                            : 'file'
                        : null,
                    action: action
                })
                .then(res => {
                    this.selected = []
                    this.$notify({
                        group: 'note',
                        type: res.status ? 'success' : 'error',
                        text: res.status
                            ? action == 'trash'
                                ? '    '
                                : action == 'file'
                                    ? ' ' + res.name + '<br/>.'
                                    : ' ' + res.name + '<br/>.'
                            : res.name + '<br/>' + res.text
                    })
                    resolve(true)
                })
            })
        },
        onRemoveSelected(){
            let list = this.selected
            let listPromises = []
            list.map(l => {
                listPromises.push(this.removeItem(l, l.type))
            })
            Promise.all(listPromises)
            .then(res => {
                this.selected = []
                return this.$store.dispatch('workflow/getRemovedFiles')
            })
            .then(res => {
                this.items = res
            })
        }
    },
    created(){
        this.$store.dispatch('workflow/getRemovedFiles').
        then(res => {
            this.items = res
        })
    }
}
</script>
